import * as types from "./types";
 import { baseUrl,headers } from "../config";
import Axios from 'axios'

export const getDiscovery = (obj,scanId) => {
       return function action(dispatch) {
        return Axios.get(baseUrl + `/discovery/getAll/${scanId}?pageNumber=${obj.pageNumber}&search=${obj.search}&limit=${obj.limit}`,
        headers).then(res=>{
             let  response = res['data']
                 if (res.status === 200) {
                   return  dispatch( {type: types.GET_DISCOVERY_LIST,payload:response} )
                  }else{
                   return dispatch( {type:types.ERROR,payload:response})
                  }      
                }).catch(err => {
                    return dispatch({  type: types.CATCH_ERROR,
                     payload: err})
           })
         }
}
export const getMyDiscovery = (id) => {
  return function action(dispatch) {
   return Axios.get(baseUrl + `/discovery/${id}`,headers).then(res=>{
        let  response = res['data']['data']
            if (res.status === 200) {
              return  dispatch( {type: types.GET_SCAN_IMAGES,payload:response} )
             }else{
              return dispatch( {type:types.ERROR,payload:response})
             }      
           }).catch(err => {
               // return dispatch({  type: types.CATCH_ERROR,
               //  payload: err})
      })
    }
}
export const createDiscovery = (obj) => {
       return Axios.post(baseUrl + `/discovery/create`,obj,headers).then(res=>{
           let  response = res['data']['data']
                  return {payload:response,res} 
                     
              }).catch(err => {
                return {payload:err} 
         })
      
}

export const deleteDiscovery = (id) => {
  return Axios.delete(baseUrl + `/discovery/delete/${id}`,headers).then(res=>{
      let  response = res['data']
             return {payload:response} 
         }).catch(err => {
           return {payload:err} 
    })
}
  
export const updateDiscovery = (obj,id) => {
  console.log(obj,id)
  return Axios.put(baseUrl + `/discovery/update/${id}`,obj,headers).then(res=>{

      let  response = res['data']
             return {payload:response,res} 
         }).catch(err => {
           return {payload:err} 
    })
}
  