import { withRouter } from "react-router"; 
import * as userAction from "../actions/user.action";
import * as authAction from "../actions/auth.action";

  class Admin extends Component {
  
  constructor(props){
    super(props);  
    this.state={
        editUserId:0,
        openPopup:false,
        deletePopup:false,
        allUsers:[],
        name:"",
        email:"",
        // gender:'',
        // genderDropDownValue:['Male','Female'],
        roleType:0,
        role:[],
        popupType:"",
        confirm_password:"",
        password:"",
        search:""
    }    

  }
 geUserList=(search)=>{
     
    //     authAction.getTransulators(1, 100,search).then(res => {
    //         if (res.type === "USER_FAILURE") {
    //            toast.error(res.type.payload, {
    //                position: toast.POSITION.TOP_RIGHT
    //            });
    //        } else{
    //             this.setState({
    //                allUsers: res.payload.message
    //            })
    //        }
    //    })
     
  
 }
                              
  componentDidMount(){
      if(JSON.parse(localStorage.getItem("user")).roleType!=1){
          this.props.history.push('/patient-management')
      }
   this.geUserList('') ;
   this.getRoles();
  }
openPopup=()=>{
    this.setState({openPopup:!this.state.openPopup})
}
deletePopup=()=>{
    this.setState({deletePopup:!this.state.deletePopup})

}
deleteUser=(id)=>{
    this.setState({deletePopup:false})
     userAction.deleteUser(this.state.editUserId).then(res=>{
         if(res.statusCode===200){
            toast.success("User deleted successfully", {
                position: toast.POSITION.TOP_RIGHT
            });
            this.geUserList('') ;
        }else{
            toast.error(res.message, {
                position: toast.POSITION.TOP_RIGHT
            });
        }
    })
}
editProfile=(userObj)=>{
    this.setState({openPopup:true,
        editUserId:userObj.id,
        popupType:"edit",
        name:userObj.name,
        email:userObj.email,
        // gender:userObj.gender,
        roleType:userObj.role.id,
    })
}
getRoles=()=>{
    userAction.getRoles().then(res=>{
        if(res.statusCode===200){
            this.setState({role:res.data})

        }else{
            toast.error("Network Error", {
                position: toast.POSITION.TOP_RIGHT
            });
        }
    })
}
saveUser=(e,type,userObj)=>{
    e.preventDefault(); 
        if(type==="edit"){
        let obj={
            name:this.state.name,
            // email:this.state.email,
            // gender:this.state.gender,
            role_id:this.state.roleType,
         }
 userAction.updateUser(this.state.editUserId,obj).then(res=>{
     if(res.statusCode===200){
        this.geUserList('') ;

        toast.success("User updated successfully", {
            position: toast.POSITION.TOP_RIGHT
        });
    }else{
        toast.success(res.error, {
            position: toast.POSITION.TOP_RIGHT
        });
    }
})
    }else if(type==="save"){
        let obj={
            name:this.state.name,
            email:this.state.email,
            // gender:this.state.gender,
            role_id:parseInt(this.state.roleType),
            password:this.state.password,
            confirm_password:this.state.confirm_password
        
         }
         if(obj.password===obj.confirm_password){
            authAction.registerUser( obj).then(res=>{
                if(res.statusCode===200){
                   this.geUserList('') ;
                   toast.success("User registered successfully", {
                       position: toast.POSITION.TOP_RIGHT
                   });
               }else{
                   console.log(res.message)
                   if(res.message&&res.message.details){
                    res.message.details.forEach(element => {
                        toast.error(element.message, {
                            position: toast.POSITION.TOP_RIGHT
                        });
                       });
                   }else{
                    toast.error(res.message, {
                        position: toast.POSITION.TOP_RIGHT
                    });
                   }
                
                  
               }
           })
         }else{
            toast.error("Password and confirm password must be same", {
                position: toast.POSITION.TOP_RIGHT
            });
         }
       

    }
 
}
handleSearchUser=(e)=>{
     this.setState({[e.target.name]:e.target.value})
this.geUserList(e.target.value)
}

handleChange=(e)=>{
this.setState({[e.target.name]:e.target.value})
}
  render() {
     
    return (                
			<section role="main" className="content-body">
          




				<header className="page-header">
					<h2>User List</h2>
				</header>
              
                    
              
                <ToastContainer />
			</section>
			);
  }
}

