import React, { Component } from 'react';
import avatarImage from '../assets/images/avatar-1.jpg';
import { connect } from "react-redux";
import _ from 'lodash'
import { withRouter} from 'react-router-dom';
import * as authAction from "../actions/auth.action";
import * as userAction from "../actions/user.action";
import { ToastContainer, toast } from 'react-toastify';
import { headers,baseUrl } from "../config";
let profile={}
    class LockScreenComponent extends Component {
        constructor(props) {
            super();
            this.state = {
                password:"",
                profile:{}
                }
           }

componentDidMount(){
this.setState({profile:this.props.profile})
profile=this.props.profile
console.log(this.props.profile)

    if(_.isEmpty(profile)){
        this.props.history.push('/')
        localStorage.setItem('email',this.state.profile.email)
        // console.log(localStorage.getItem('email'))
        setTimeout(() => {
            localStorage.removeItem('intelliscope-token')
            localStorage.removeItem('session_id')
        }, 300);
    }else{
        setTimeout(() => {
            localStorage.removeItem('intelliscope-token')
            localStorage.removeItem('session_id')
        }, 300);
            // this.props.getMyProfile()

    }
    }
    componentWillReceiveProps(nextProps){
        this.setState({profile:nextProps.profile})
      }
 unlock=()=>{
      let obj={
        email:this.state.profile.email?this.state.profile.email:localStorage.getItem('email'),
        password:this.state.password
       }
    //    console.log(obj)
     authAction.login(obj).then(res=>{

        if(res.status===200){
            res=res['data']
            console.log(res)

            let user={
             userId:res.userId,
             userType:res.userType
            }
           localStorage.setItem("intelliscope-token",res.token)
           localStorage.setItem("user",user)
           headers.headers.Authorization="JWT "+res.token
            this.props.getMyProfile()
           
         if(user.userType===2||user.userType===1){
           toast.error("You are not allow to login", {
             position: toast.POSITION.TOP_RIGHT
         });
         }else{
           toast.success("Login success", {
             position: toast.POSITION.TOP_RIGHT
         });
            setTimeout(() => {
                this.props.history.goBack()
            }, 1000);
        }
         } else{
            toast.error( res.data.message.message, {
                position: toast.POSITION.TOP_RIGHT
            });    
         }
    })              
 }
 render(){
    return(
        <div>
                        <section id="LockScreenInline" class="body-sign body-locked body-locked-inline mfp-lock-screen mfp-bg">
                <div class="center-sign">
                    <div class="panel card-sign">
                        <div class="card-body">
                            <form>
                            <div class="current-user text-center">
                                <img id="LockUserPicture" src={(baseUrl+this.state.profile.profileImg?baseUrl+this.state.profile.profileImg:avatarImage)} alt={"Profile Image"} class="rounded-circle user-image"/>
                                <h2 id="LockUserName" class="user-name text-dark m-0">{this.state.profile.firstname} {this.state.profile.lastname}</h2>
                                <p id="LockUserEmail" class="user-email m-0"></p>
                            </div>
                            <div class="form-group mb-lg">
                                <div class="input-group"><input id="pwd"  
                                  name="password" value={this.state.password}onChange={e=>this.setState({password:e.target.value})} onKeyUp={e=>this.setState({password:e.target.value})}
                                   type="password" class="form-control form-control-lg" placeholder="Password"/><span class="input-group-append"><span class="input-group-text"><i class="fas fa-lock"></i></span></span></div>
                            </div>
                            <div class="row">
                            
                                <div class="col-6"></div>
                                <div class="col-6">
                                <input type="button" class="btn btn-primary pull-right" value="Unlock" onClick={e=>this.unlock()} />
                                 </div>
                            </div>
                            </form>
                        </div>
                    </div>
                </div>
                </section>
                <ToastContainer/>
        </div>
    )
}
} 
const mapStateToProps = (state) => {
    return {
      profile:state.login.my_profile
     }
  }
  const mapDispatchToProps = (dispatch) => {
  return {
    getMyProfile:(obj)=>{dispatch(userAction.getMyProfile())}

   }
  }
  export default withRouter(connect(mapStateToProps, mapDispatchToProps)(LockScreenComponent));
  
  