import * as types from "./types";
 import { baseUrl,headers } from "../config";
import Axios from 'axios'

export const getRecentScans = (obj,patientId) => {
       return function action(dispatch) {
        return Axios.get(baseUrl + `/scanDetail/getAll/${patientId}?pageNumber=${obj.pageNumber}&search=${obj.search}&limit=${obj.limit}`,
        headers).then(res=>{
             let  response = res['data']['data']
                if (res.status === 200) {
                   return  dispatch( {type: types.GET_RECENT_SCANS,payload:response} )
                  }else{
                   return dispatch( {type:types.ERROR,payload:response})
                  }      
                }).catch(err => {
                    // return dispatch({  type: types.CATCH_ERROR,
                    //  payload: err})
           })
         }
}

export const getScanImages = (obj) => {
    return function action(dispatch) {
     return Axios.get(baseUrl + `/scan/getImages?existImageCount=0&patientId=${obj.patientId}&scanId=${obj.scanId}`,headers).then(res=>{

          let  response = res['data']['data']
              if (res.status === 200) {
                return  dispatch( {type: types.GET_SCAN_IMAGES,payload:response} )
               }else{
                return dispatch( {type:types.ERROR,payload:response})
               }      
             }).catch(err => {
                 // return dispatch({  type: types.CATCH_ERROR,
                 //  payload: err})
        })
      }
}
export const createNewScan = (obj) => {
       return Axios.post(baseUrl + `/scanDetail/create`,obj,headers).then(res=>{
           let  response = res['data']['data']
                  return {payload:response} 
                     
              }).catch(err => {
                return {payload:err} 
         })
}
export const updateScan = (obj,sessionId) => {
  return Axios.put(baseUrl + `/scanDetail/update/${sessionId}`,obj,headers).then(res=>{
      let  response = res['data']
             return response 
                
         }).catch(err => {
           return {payload:err} 
    })
}
export const deleteScan = (id) => {
    
    return Axios.delete(baseUrl + `/scanDetail/delete/${id}`,headers).then(res=>{
        let  response = res['data']['data']
               return {payload:response} 
           }).catch(err => {
             return {payload:err} 
      })
}
export const createScanImages = (obj) => {
    return Axios.post(baseUrl + `/scan/createImage`,obj,headers).then(res=>{
        let  response = res['data']['data']
               return {payload:response} 
                  
           }).catch(err => {
             return {payload:err} 
      })
   
}

