import React, { Component } from 'react';
import { withRouter} from 'react-router-dom';
 import { connect } from "react-redux";
import _ from 'lodash'
import Webcam from "react-webcam";
import moment from "moment"
import Popup from "reactjs-popup";
import * as scanAction from "../actions/scan.action";
import * as mlApiAction from "../actions/mlApi.action";
import * as discoveryAction from "../actions/dicovery.action";
import { baseImageUrl,baseDiscovery } from "../config";
import 'rc-slider/assets/index.css';
 import Slider from 'rc-slider';
 import Tooltip from 'rc-tooltip';
import 'rc-tooltip/assets/bootstrap.css';
import '../assets/css/theme.css'
import '../assets/css/skins/default.css'
import '../assets/css/custom.css'
import { ToastContainer, toast } from 'react-toastify';

let time=0
  let  rects=[]
  let coordinate={}
let contexts = ""
let image = new Image;
let discoveryArray=[]
class ScanConponent extends Component {
    constructor(props) {
        super();
        this.state = { 
            imageSets:[],
            statusOption:['Open','Closed','Attention'],
            scanObject:{},
            playInd:0,
            discoveryName:"",
            discoveryDetail:"",
            showPopup:false,
            recording:false,
            webCamError: "",
            imageSrc:"",
            playingExistingVideo:false,
            mouseX:null,
            mouseY:null,
            takeCoordinate:false,
            sourceImageBase64:'',
            discoveryArray:[],
            playingImage:false,
            createDiscovery:false,
            patientDetail:{},
            viewDiscoveryImage:"",
            discoveryObj:{},
            camWidth:640,
            fps:200,
            status:""
          }
            this.playInd=0
            this.dr=null
            this.playImageIn=null
            this.startRecordInterval=null
      }
      componentDidMount(){
        console.log(this.props.scanObject,"@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@",this.props.patientDetail)
        this.setState({patientDetail:this.props.patientDetail})
        if(_.isEmpty(this.props.scanObject)){
          this.HistoryPush("/patient_details")
             }else{ 
                   this.setState({scanObject:this.props.scanObject,...this.props.scanObject},()=>{
                    this.props.getScanImages({scanId:this.state.scanObject.id,patientId:this.state.scanObject.patientId,existImageCount:0})   
                    this.props.getAllDiscovery({pageNumber:1,limit:10,search:''},this.state.scanObject.id)
                    // this.getScanImages()
                   })
             }
      }
      getScanImages=()=>{
        this.props.getScanImages({scanId:this.state.scanObject.id,patientId:this.state.scanObject.patientId,existImageCount:0})   
       }
      
      componentWillReceiveProps(nextProps){
            if(!_.isEmpty(nextProps.discoveryList)){
                discoveryArray=nextProps.discoveryList.result 
                this.setState({imageSets:nextProps.scanImageSets,discoveryArray:nextProps.discoveryList.result})
            }
            
            if(nextProps.scanImageSets.length>0){
                this.setState({imageSets:nextProps.scanImageSets})
            }
      }

      HistoryPush=(path)=>{
        this.props.history.push(path)
      
     }
    setRef = webcam => {
        this.webcam = webcam;
    };

    startRecord=()=>{
     this.setState({playingImage:false,recording:true,playingExistingVideo:false})
if(this.state.webCamError===""){
    this.startRecordInterval = setInterval(() => {
        if(this.state.playingExistingVideo===false&&this.state.playingImage===false){
            this.capture();
        }
    }, this.state.fps);
}else{
     toast.error(this.state.webCamError, {
        position: toast.POSITION.TOP_RIGHT
    });
    this.setState({recording:false})
}
       }

    stopRecord=()=>{
        console.log("stoped",new Date())
        this.getScanImages()
        this.setState({recording:false})
        clearInterval(this.startRecordInterval)
    }
    deleteScan=()=>{
        let self=this

        var formData = new FormData();
         formData.append("scan_id",self.state.scanObject.id)
        formData.append("patient_id",self.state.scanObject.patientId)
        formData.append("process","stop")
        formData.append("timeStamp", Date.now())

        mlApiAction.Inference(formData).then(data=>{
          if(data.status===200){
             self.setState({recording:false,imageSets:[]})
            clearInterval(self.startRecordInterval)
             setTimeout(() => {
                 self.setState({imageSrc:"",recording:false})
             }, this.state.fps);
             toast.success(data.result, {
                position: toast.POSITION.TOP_RIGHT
            });
          }else{
               toast.error("Something went wrong", {
                position: toast.POSITION.TOP_RIGHT
            });
             }
           
        })
       }
    capture = () => {
        time=time+1
        console.log(time,"Capturing",new Date())

         let imageSrc = this.webcam.getScreenshot();
          let context = this.canvasElement.getContext('2d');
          contexts=this.canvasElement.getContext('2d');
           context.drawImage(image, 0, 0);
         image.src = imageSrc
        //   let createImageObj={patientId:this.state.scanObject.patientId,scanId:this.state.scanObject.id,file:imageSrc}
        //  scanAction.createScanImages(createImageObj).then(result=>{
        //     //  console.log("Creating... image")
        //  })

         var formData = new FormData();
         formData.append("image",imageSrc)
         formData.append("scan_id",this.state.scanObject.id)
         formData.append("patient_id",this.state.scanObject.patientId)
         formData.append("process","start")
         formData.append("timeStamp", Date.now())

         context.beginPath();
         mlApiAction.Inference(formData).then(data=>{
            console.log(data)
            context.lineWidth = "2";
            context.strokeStyle = "red";
             if(data.status===200){
               rects=[]
               if(this.canvasElement){
                contexts.clearRect(0,0,this.canvasElement.width,this.canvasElement.height); 
                contexts.drawImage(image, 0, 0);
               }
              
               data.result.forEach((element,index)=>{
                   
              //    context.fillText("Person "+index, element.x, element.y);
              
              //method not required 
            //   imageArray.forEach((element,index)=>{
            //    context.strokeStyle=element.labelColor?element.labelColor: 'red'
            //    context.fillStyle=element.labelColor?element.labelColor: 'red'
       
            //     context.rect(element.x, element.y, element.w, element.h)
            //    context.strokeRect(element.x, element.y, element.w, element.h)
            //    context.beginPath();
            //    context.fillText(element.label, element.x-3, element.y-5);

            //   })

                context.font ="bold 24px verdana, sans-serif "
                 context.rect(element.x, element.y,  element.w,element.h,)
                 context.fillText(element.discoveryName, element.x-3, element.y-5);

                  rects.push({x: element.x, y: element.y, w: element.w, h: element.h})
                 context.stroke()
               })
               context.beginPath();

             }else{
                toast.error(data.Message, {
                    position: toast.POSITION.TOP_RIGHT
                });
                 console.log("Error in api",data)
             }
        })
          this.setState({imageSrc:imageSrc})
     };

    onUserMediaError = (event) => {
        
         this.setState({
           webCamError:"Cam not found"
        })
    }
         
     saveImageWithCoordinates=(XValue,YValue)=>{
        //  console.log(XValue,YValue)
     coordinate={}
    rects.forEach(element=>{
       if(element.x===XValue&&element.y===YValue){
        coordinate= element
        
      }
    })
        // console.log(coordinate)

        // if(!_.isEmpty(coordinate)){
        //   this.setState({formData})
        // }
     
    
     }
      collides(rectss, x, y) {
      var isCollision = false;
      for (var i = 0, len = rectss.length; i < len; i++) {
          var left = rectss[i].x, right = rectss[i].x+rectss[i].w;
          var top = rectss[i].y, bottom = rectss[i].y+rectss[i].h;
          // console.log(left,top)
          if (right >= x
              && left <= x
              && bottom >= y
              && top <= y) {
              isCollision = rectss[i];
          }
      }
      return isCollision;
    }
  
      handleMouseClick=(e)=>{
       function collides(rectss, x, y) {
            var isCollision = false;
            for (var i = 0, len = rectss.length; i < len; i++) {
                var left = rectss[i].x, right = rectss[i].x+rectss[i].w;
                var top = rectss[i].y, bottom = rectss[i].y+rectss[i].h;
                if (right >= x
                    && left <= x
                    && bottom >= y
                    && top <= y) {
                    isCollision = rectss[i];
                }
            }
            return isCollision;
          }


          var rect = collides(rects, e.nativeEvent.offsetX, e.nativeEvent.offsetY);
          console.log(rect,e.nativeEvent.offsetX,e.nativeEvent.offsetY)
          if (rect) {
             this.setState({showPopup:true,createDiscovery:true})
             return this.saveImageWithCoordinates(rect.x ,rect.y)  
          } else {
              console.log('no collision');
          }
        this.setState({
          mouseX:e.nativeEvent.offsetX,//e.screenX,
          mouseY:e.nativeEvent.offsetY,//e.screenY,
          shallMouseMove:true
        })
      }

componentDidUpdate(){
    // image.onload = function() {
    //     contexts.drawImage(image, 0, 0);
    // };
}
      handleMouseEvent=(e)=>{
         if(this.state.shallMouseMove==true){ 
          this.setState({takeCoordinate:true})
          let width=e.nativeEvent.offsetX- this.state.mouseX;
          let height=e.nativeEvent.offsetY- this.state.mouseY
    
          contexts.clearRect(0,0,this.canvasElement.width,this.canvasElement.height); 
          contexts.drawImage(image, 0, 0);

          contexts.lineWidth = "2";
          contexts.strokeStyle = 'red';
          contexts.strokeRect(this.state.mouseX, this.state.mouseY, width,height);
        }
      }

      handleMouseRelease=(e)=>{
        // console.log("clientX : ",e.clientX)
        // console.log("clientY : ",e.clientY)
        // console.log("currentTarget : ",e.currentTarget)
        // console.log("pageX : ",e.pageX)
        // console.log("pageY : ",e.pageY)
        // console.log("screenX : ",e.screenX)
        // console.log("screenY : ",e.screenY)
        // console.log("target : ",e.target)
        // console.log("view : ",e.view)

        
        
        const context = this.canvasElement.getContext('2d');
        if(this.state.shallMouseMove==true&&this.state.takeCoordinate==true){
            // image.src =this.state.imageSrc
          this.setState({
              
            takeCoordinate:false})
          let width=(e.nativeEvent.offsetX-this.canvasElement.offsetLeft)- this.state.mouseX;
          let height=(e.nativeEvent.offsetY-this.canvasElement.offsetTop)- this.state.mouseY
           rects.push({x: this.state.mouseX, y: this.state.mouseY, w: width, h: height})

           rects.forEach((element,index)=>{
            // context.fillText("Person "+index, element.x, element.y);
            context.rect(element.x, element.y, element.w, element.h)
            //  rects.push({x: element.x, y: element.y, w: element.w, h: element.h})
            context.stroke()
            context.beginPath();
          })
         }
  
        this.setState({
          takeCoordinate:false,
          shallMouseMove:false
        })
      }
       
    handleChange=(e)=>{
    this.setState({[e.target.name]:e.target.value})
    }
    
     closeHandle=()=>{
        this.setState({showPopup:!this.state.showPopup,
           discoveryDetail:"",
           viewDiscoveryImage:"",
           discoveryName:""
       })
       }
    
       saveDiscovery=(type)=>{
   if(type==="save"){
        let obj={
            scanId:this.state.scanObject.id,  
            detail:this.state.discoveryDetail,
            name:this.state.discoveryName                            
        }
        console.log(this.state.discoveryObj)
        if(obj.name===""){
            toast.error("Discovery name is required", {
                position: toast.POSITION.TOP_RIGHT
            });
        }else{
            discoveryAction.createDiscovery(obj).then(result=>{
                console.log(result)
                var formData = new FormData();
                formData.append("image",this.state.imageSrc)
                formData.append("timeStamp", Date.now())
                formData.append("d_name",this.state.discoveryName)
                formData.append("patient_id",this.state.scanObject.patientId)
                formData.append("x",coordinate.x)
                formData.append("y",coordinate.y)
                formData.append("w",coordinate.w)
                formData.append("h",coordinate.h)
                formData.append("id",result.payload.id)
    
                mlApiAction.discovery( formData).then(data=>{
                    console.log(data)
                    this.props.getAllDiscovery({pageNumber:1,limit:10,search:''},this.state.scanObject.id)
                    this.setState({showPopup:false,
                        discoveryDetail:"",
                        discoveryName:""
                        })
                })
               
            })
        }
        
         }else{
             let obj={
               detail:this.state.discoveryDetail,
               name:this.state.discoveryName                            
               }
              discoveryAction.updateDiscovery(obj,this.state.discoveryObj.id).then(result=>{
                   this.props.getAllDiscovery({pageNumber:1,limit:10,search:''},this.state.scanObject.id)
                  this.setState({showPopup:false,
                       discoveryDetail:"",
                      discoveryName:""
                      })
              })

        }
       
   
       }
        
       removeDiscovery=(obj,index)=>{
        discoveryAction.deleteDiscovery(obj.id).then(result=>{
            console.log(result)
            })
       discoveryArray.splice(index,1)
       this.setState({discoveryArray})
       }
       saveScan=()=>{
        var formData = new FormData();
         formData.append("scan_id",this.state.scanObject.id)
        formData.append("patient_id",this.state.scanObject.patientId)
        formData.append("process","save")
        formData.append("timeStamp", Date.now())

        mlApiAction.Inference(formData).then(result=>{
            console.log(result)
            if(result&&parseInt(result.status)===200){
                toast.success("Scan Video is saved successfully", {
                    position: toast.POSITION.TOP_RIGHT
                });
              this.props.getScanImages({scanId:this.state.scanObject.id,patientId:this.state.scanObject.patientId,existImageCount:0})   
    
            }else{
                toast.error("Error saving the video", {
                    position: toast.POSITION.TOP_RIGHT
                });
            }
         
        })
         
       }
       
       playExistingVideo=()=>{
        
           this.playImageIn= setInterval(() => {

              if(!this.state.imageSets[this.playInd+2]){
               this.setState({playingImage:false})
               this.playInd=0
                clearInterval(this.playImageIn)
              }else{
                // let context = this.canvasElement.getContext('2d');
                // context.drawImage(image, 0, 0);
                // console.log(context,image)
                // image.src=baseImageUrl+this.state.imageSets[this.playInd]

                this.setState({playInd:this.state.playInd+1,playingImage:true,recording:false,playingExistingVideo:true,imageSrc:this.state.imageSets[this.playInd]},()=>{
                 })
                this.playInd=this.playInd+1
              }

           },  this.state.fps);

        }
    stopPlaying=()=>{
        this.setState({playingImage:false,
            imageSrc:this.state.imageSets[this.playInd]
        });
        this.stopRecord();
        clearInterval(this.playImageIn)
    }
 switchCam=()=>{
this.setState({camWidth:this.state.camWidth===640?642:640})
 }
      createDiscovery=()=>{

return<div>
<div className="model-close p-2">
<h3 className="m-0 d-inline-block"> Create discovery </h3>
     <a className="close" onClick={e=>this.closeHandle()}>
      &times;
      </a>
 </div>
<div className="p-3">
    <label className="title">Discovery name</label>
    <input type="text" className="description truncate form-control" name="discoveryName" onChange={e=>this.handleChange(e)} value={this.state.discoveryName} /><br/>
    <lable className="title">Discovery detail</lable>
    <input type="text" className="description truncate form-control" name="discoveryDetail" onChange={e=>this.handleChange(e)} value={this.state.discoveryDetail} />

<label className="title">Discovery name</label>
<input type="text" className="description truncate"name="discoveryName" onChange={e=>this.handleChange(e)} onKeyUp={e=>this.handleChange(e)} value={this.state.discoveryName} /><br/>
<lable className="title">Discovery detail</lable>
<input type="text" className="description truncate"name="discoveryDetail" onChange={e=>this.handleChange(e)} onKeyUp={e=>this.handleChange(e)} value={this.state.discoveryDetail} />
<div className="btn-group btn-group-sm flex-wrap float-right">
<a className="btn btn-sm btn-success" onClick={e=>this.saveDiscovery("save")} ><i className="fas fa-save"></i> Save</a>
</div>
</div>
</div>
    }
    editDiscovery=()=>{
        return <div>
        <div className="model-close">
             <b> Edit discovery </b>
                 <a className="close" onClick={e=>this.closeHandle()}>
                 &times;
                 </a>
       </div>
     <label className="title">Discovery name</label>
     <input type="text" className="description truncate"name="discoveryName" onChange={e=>this.handleChange(e)} onKeyUp={e=>this.handleChange(e)} value={this.state.discoveryName} /><br/>
     <lable className="title">Discovery detail</lable>
     <input type="text" className="description truncate"name="discoveryDetail" onChange={e=>this.handleChange(e)} onKeyUp={e=>this.handleChange(e)} value={this.state.discoveryDetail} />
     <div className="btn-group btn-group-sm flex-wrap float-right">
     <a className="btn btn-sm btn-success text-white" onClick={e=>this.saveDiscovery("edit")} ><i className="fas fa-save"></i> Update</a>
         </div>
     </div>
    }

    saveScanDetail=()=>{
         let obj={status:this.state.status,scanName:this.state.scanName,updated_at:this.state.updated_at}
        console.log(obj)
        scanAction.updateScan(obj,this.state.id).then(result=>{
            if(result){
                if(result.statusCode===200){
                    toast.success(result.data.message, {
                        position: toast.POSITION.TOP_RIGHT
                    });
                 }else{
                     toast.error(result.data.message, {
                        position: toast.POSITION.TOP_RIGHT
                    });  
                }
            }else{
                toast.error("Something went wrong", {
                    position: toast.POSITION.TOP_RIGHT
                }); 
            }
         })
    }
    
    render() {
        // console.log(this.state.imageSets)
        const createSliderWithTooltip = Slider.createSliderWithTooltip;
        const Range = createSliderWithTooltip(Slider.Range);
        const videoConstraints = {
            width: this.state.camWidth,
            height: 480,
         };
        const Handle = Slider.Handle;
        const handle = (props) => {
            const { value, dragging, index, ...restProps } = props;
            // console.log(value,props)
            return (
                <Tooltip
                    prefixCls="rc-slider-tooltip"
                    overlay={value}
                    visible={dragging}
                    placement="top"
                    key={index}
                >
                    <Handle value={value} {...restProps} />
                </Tooltip>
            );
        }
          return (
            <div>
                  {this.state.showPopup===true?

                   <Popup
                   open={this.state.showPopup}
                   closeOnDocumentClick
                   onClose={e=>this.setState({viewDiscoveryImage:"",showPopup:!this.state.showPopup})}
                  >
                    {this.state.viewDiscoveryImage===""?
                        this.state.createDiscovery===true?this.createDiscovery():this.editDiscovery()
                    :
                    <div className="model-close">
                    <div>
                    <b> Discovery Image </b>
                        <a className="close" onClick={e=>this.closeHandle()}>
                        &times;
                        </a>
                    </div>
                        <img src={this.state.viewDiscoveryImage} alt={this.state.viewDiscoveryImage} 
                    className="embed-responsive-item" />
              </div>

                   
                        
                    }
                 
                 </Popup>

                :null}
                <section role="main" className="content-body">
					<header className={'page-header ' + (this.state.showMenu ? '' : 'header-left')}>
						<h2>Scan - {this.state.patientDetail?this.state.patientDetail.firstname+" "+this.state.patientDetail.lastname:"test"}</h2>
					</header>
                   
                   <div className="row scan-body flex-col">
						<div className="col-xl-9 col-lg-12 col-md-12 col-sm-12 mb-4 mb-xl-0 mt-sm-3 mydiv">
							<section className="card">
								<div className="card-body">
									<div className="row">
                                        <div className="col-12">
                                            <div className="embed-responsive embed-responsive-21by9 obj-fit-cover marg-auto">
                                             {this.state.playingExistingVideo===true&&this.state.imageSrc?

                            this.state.imageSrc.split("-").length>1?
                            <img src={baseImageUrl+this.state.imageSrc} alt={baseImageUrl+this.state.imageSrc} 
                            className={this.state.imageSrc!==""?"embed-responsive-item ":"embed-responsive-item d-none"} /> :
                                                                    
                            <img src={this.state.imageSrc} alt={this.state.imageSrc} 
                            className={this.state.imageSrc!==""?"embed-responsive-item ":"embed-responsive-item d-none"} />
                                                   
                                                
                                                : <canvas id="canvas"
                                                onMouseUp={(e)=>this.handleMouseRelease(e)} 
                                                onMouseDownCapture={(e)=>this.handleMouseClick(e)}
                                                onMouseMove={(e,canvas)=>this.handleMouseEvent(e,canvas)}
                                              className={this.state.imageSrc!==""?"embed-responsive-item":"embed-responsive-item d-none"}
                                              width="640" height="480" 
                                              ref={(input) => this.canvasElement = input} />}
                                        {/* {this.state.playingExistingVideo===true? <div className="position-absolute w-100 px-5 text-center" style={{ bottom: '0px' }} >
                                <div className="btn-group btn-group-sm flex-wrap mb-1"> 
                                    <button type="button" disabled={this.state.disPrev} onClick={this.handlePrev} className="btn btn-dark"><i className="fa fa-fast-backward"></i> Rewind</button>
                                    <button type="button" disabled={!this.state.playDisable} onClick={this.handlePause} className="btn btn-dark"><i className="fa fa-pause text-warning"></i> Pause</button>
                                    <button type="button" disabled={this.state.playDisable} onClick={this.handlePlay} className="btn btn-dark"><i className="fa fa-play text-success"></i> Play</button>
                                    <button type="button" disabled={this.state.disNext} onClick={this.handleNext} className="btn btn-dark"> Next <i className="fa fa-fast-forward "></i></button>
                                    <button type="button" onClick={this.handleImage} className="btn btn-dark"><i className="fa fa-video-camera text-danger"></i> Record</button>
                                </div>
                                <div className="slidecontainer">
                                    <div className="mb-3 mt-0 slider-primary">
                                        <Slider min={0} max={this.state.imageSets - 1} value={this.playInd} handle={handle} defaultValue={[0, this.state.imageSets-1]} onAfterChange={(e) => this.handleRange(e, this.playInd)} />

                                    </div>
                                </div>
                            </div>:null} */}
                                              
                                           
                                            {this.state.webCamError===""?
                                             <Webcam className={this.state.imageSrc===""?"embed-responsive-item":" embed-responsive-item d-none"}  
                                              audio={false}   ref={this.setRef}   
                                               videoConstraints={videoConstraints}
                                            screenshotFormat="image/jpeg" onUserMediaError={e=>this.onUserMediaError(e)}  />
                                            :<h1 className="embed-responsive-item fs18 text-center" style={{color:"red"}}>Webcam not found!</h1>} 
                                           
                                               

                                            </div>
                                        </div>
                                    </div>
								</div>
                     
							</section>
						</div>
						<div className="col-xl-3 col-lg-12 col-md-12 col-sm-12 mydiv">
							<section className="card mt-3 mb-3">
                            	<header className="card-header">
                                    <button className="btn btn-primary" onClick={e=>{this.switchCam()}}>cam Switch <i class="fas fa-sync-alt"></i></button>
                                    <h2 className="card-title mt-2">Scan Results</h2> 
                                    <form method="">
	                                    <div className="form-group row">
                                            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 mt-1">
                                            	<div className="form-group">
                                            		<label className="col-form-label" htmlFor="inputTitle">Title</label>
                                                    <input type="text" className="form-control form-control-sm"
                                                    name='scanName' 
                                                    onChange={e=>this.handleChange(e)}
                                                    id="inputTitle" value={this.state.scanName} />
												</div>
                                        	</div>
                                            <div className="col-xl-12 col-lg-6 col-md-6 col-sm-6 mt-1 dividing">
                                                <div className="form-group">
                                                	<label className="col-form-label" htmlFor="selectStatus">Status</label>
                                                    <select  name ="status" value={this.state.status} 
                                                    onChange={e=>{this.handleChange(e)}} onKeyUp={e=>{this.handleChange(e)}} className="form-control form-control-sm" id="selectStatus">
                                                    <option  value="">please select</option>
                                                    {this.state.statusOption.map((status,index)=> 
                                                      <option key={index} value={status} >{status}</option>
                                                    )}
                                                    </select>
                                                </div>
                                            </div>
                                            <div className=" col-xl-12 col-lg-6 col-md-6 col-sm-6 mt-1 dividing">
                                                <div className="form-group">
                                                    <label className="col-form-label" htmlFor="selectScanDate">Scan Date</label>
                                                    <div className="input-group">
                                                        <span className="input-group-prepend">
                                                            <span className="input-group-text">
                                                                <i className="fas fa-calendar-alt fs12" ></i>
                                                            </span>
                                                        </span>
                                                        <input type="text" data-plugin-datepicker 
                                                        className="form-control form-control-sm"
                                                        // onChange={e=>this.handleChange(e)}
                                                        name="updated_at"
                                                         value={moment.utc(this.state.created_at).utcOffset(moment().format('Z')).format('MM/DD/YYYY') } 
                                                         id="selectScanDate" />
                                                    </div>
                                                </div>
                                            </div>

                                  		</div>
                                          
                                          <button type="button" onClick={e=>{this.saveScanDetail()}} 
                                            className="btn btn-primary  border-0 txt-white float-right mr-1 mt-2">
                                                <lable className="text-white"><i className=" fas fa-save"></i> Save</lable></button>
                                         
                                    </form>
                                </header>
								<div className="card-body">
                                	<h5 className="mb-2 mt-3">{this.state.recording==true?"Scan in progress...":"Tap start to scan"}</h5>                                    
                                    {this.state.imageSets.length>0?
                                    this.state.playingImage===true?
                                    <button type="button" className="btn btn-default" onClick={e=>{this.stopPlaying()}}><i className="fas fa-stop text-warning mt-1 mr-1"></i>Stop</button>
                                    :<button type="button" className="btn btn-default" onClick={e=>{this.getScanImages();this.playExistingVideo()}}><i className="fas fa-play text-success mt-1 mr-1"></i>Play Recorded Video</button>
                                    :null}
                                    <div className="btn-group btn-group-sm flex-wrap mt-1">
                                       {this.state.recording===true?
                                       <button type="button" className="btn btn-default" onClick={e=>this.stopRecord()}><i className="fas fa-stop text-warning mt-1 mr-1"></i>stop</button>
                                       :
                                        <button type="button" className="btn btn-default" onClick={e=>{this.stopPlaying();this.setState({playingImage:false,imageSrc:""});this.startRecord()}}><i className="fas fa-play text-success mt-1 mr-1"></i>Start</button>
                                        } 
                                        <button type="button" className="btn btn-default" onClick={e=>{this.saveScan(e)}}><i className="fas fa-save text-info mt-1 mr-1"></i>Save</button>

                                        <button type="button" className="btn btn-default"  onClick={e=>this.deleteScan()}><i className="fas fa-stop text-danger mt-1 mr-1"></i>Delete</button>
                                        {/* <button type="button" className="btn btn-default"><i className="fas fa-trash-alt text-warning mt-1 mr-1"></i>Delete</button> */}
                                    </div>
                                    <hr className="dotted short" />
                                    <ul className="simple-bullet-list mb-3">
                                    
                                    {discoveryArray.map((discoveryObj,index) =>
                                         <li key={index} className=" green">
                                         <div className="btn-group btn-group-sm flex-wrap float-right">
                                             <a className="btn btn-sm btn-default modal-with-form pointer" onClick={()=>{this.setState({createDiscovery:false,showPopup:true,discoveryName:discoveryObj.name,discoveryDetail:discoveryObj.Detail,discoveryObj})}} ><i className="fas fa-edit text-warning"></i> Edit</a>
                                             <a className="btn btn-sm btn-default pointer" onClick={()=>this.removeDiscovery(discoveryObj,index)} ><i className="fas fa-ban text-danger"></i> Remove</a>
                                         </div>
                                         <span className="title pointer" onClick={e=>this.setState({showPopup:true,viewDiscoveryImage:baseDiscovery+"/"+discoveryObj.path})}>{discoveryObj.name}</span>
                                         <span className="description truncate">{discoveryObj.Detail}.</span>
                                     </li>     
                                       )}
                                       
                                    </ul>
                                   
								</div>
                           	</section>
                        </div>
                        </div>
                   
                </section>
                <ToastContainer/>
            </div>
        )
    }
}
 const mapStateToProps = (state) => {
      return {
        scanObject:state.scanReducer.selected_scan,
        discoveryList:state.discoveryReducer.discoveryList,
        scanImageSets:state.scanReducer.scanImages,
        patientDetail:state.patientReducer.patientObj,

    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        getAllDiscovery:(scanObj,scanId)=>{dispatch(discoveryAction.getDiscovery(scanObj,scanId))},
        getScanImages:(scanObj)=>{dispatch(scanAction.getScanImages(scanObj))},

        
     
    }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ScanConponent));