import React, { Component } from 'react';
 import '../assets/css/theme.css'
import '../assets/css/skins/default.css'
import { connect } from "react-redux";
import { ToastContainer, toast } from 'react-toastify';
 import * as mlAction from "../actions/traing_model.action";
 import Popup from "reactjs-popup";
 import _ from 'lodash'
 import ReactPaginate from 'react-paginate';

 let  rects=[]
let contexts = ""
let image = new Image;

let rectangleIndex=0
let array=[]
let annotatedImages=[]
let coordinate={}
let previousStateValue={}
  class  TrainingModel extends Component {
  
  constructor(props){
    super(props);      
    this.state={
        
  file:"",
  imageIndex:0,
  imageArray:[],
  imageIndex:0,
  imageBlob:"",
  discoveryName:"",
  shallMouseMove:false,
  
    mouseX:null,
  mouseY:null,
  takeCoordinate:false,

  showPopup:false,
  createDiscovery:false,
  mouseReleaseEvent:"",
  rectangleIndex:0,
   search:0,
   searchDiscoveries:[],
  discoveryNames:[],
  projectName:"",
  getProjectName:false,
  goTo:1,
  annotatedImages:[],
  limit:10,
  pageNumber:1,
  pickedFolderName:""
}
  }
  
  handleChange=(e)=>{
    let name=e.target.name
    let value=e.target.value
    this.setState({[e.target.name]:e.target.value},()=>{
         if(name==="goTo"&&value!=""){
                this.imageHandler("",value)
              }
    })
   
           let currentDiscovery=[]
              if(e.target.name==="discoveryName"){
                _.forEach(this.state.discoveryNames,(name,index)=>{
                   if(name.indexOf(e.target.value)!=-1){
                    currentDiscovery.push(name)
                  }
                })
                if(e.target.value!=""){
                  this.setState({search:1,searchDiscoveries:currentDiscovery})
                }else{
                  this.setState({search:0})
                }
              }
             
       } 

  pickFolder=(e)=>{
 
    this.setState({showPopup:true, getProjectName:true})
          array=[]
       for(let i=0;i<e.target.files.length;i++){
         let element=e.target.files[i]
         if(element.type==="image/png"||element.type==="image/jpeg"||element.type==="image/jpg"||element.type==="image/jpeg")   {
          e.target.files[i].discoveryArray=[]
          // console.log(e.target.files[i].name)
            array.push(e.target.files[i])

          } 
      }
      this.setState({pickedFolderName:e.target.files[0].webkitRelativePath.split('/')[0]})
    }

    imageHandler=(type,nextIndex)=>{
      // console.log(type,nextIndex)
      this.setState({getProjectName:false,showPopup:false, createDiscovery:false})
      let self=this
      let index=0
              if(nextIndex){
                  index=parseInt(nextIndex-1)
              }else{
                    if(type==">>"){
                    index= self.state.imageIndex+1
                  }else if(type=="<<"){
                    index= self.state.imageIndex-1
                  }
                }
        this.loadImage(index)
    }

 loadImage=(index)=>{
           
        let self=this
          if(self.state.imageArray[index]){
              let annotated=_.find(self.state.annotatedImages,{file_name:self.state.imageArray[index].name})
            if(annotated && self.state.imageArray[index].discoveryArray.length===0){
                  self.state.imageArray[index].discoveryArray=annotated.rect
                }
          var reader = new FileReader();
            reader.onload = function()
                    {
                      self.setState({
                      imageIndex:index,
                      imageBlob: reader.result
                    },()=>{

                      contexts=self.canvasElement.getContext('2d');
                      // let context = self.canvasElement.getContext('2d');
                    image.onload = function() {
                      
                      contexts.clearRect(0,0,self.canvasElement.width,self.canvasElement.height); 
                      contexts.drawImage(image, 0, 0,image.width,image.height,0,0,640,480);
                      rects= self.state.imageArray[index].discoveryArray
                      if(self.state.imageArray[index]&&self.state.imageArray[index].discoveryArray&&self.state.imageArray[index].discoveryArray.length>0){
                        
                        self.drawRectangle(self.state.imageArray[index].discoveryArray)
                      }
                    };
                      image.src =self.state.imageBlob 
                      })
                    }
            reader.readAsDataURL(self.state.imageArray[index]);
          }else{
            toast.warn("No Image found", {
              position: toast.POSITION.TOP_RIGHT
          });
          }
         }
     
  handleMouseRelease=(e)=>{
          if(this.state.shallMouseMove==true&&this.state.takeCoordinate==true){
            let width=e.nativeEvent.offsetX- this.state.mouseX;
            let height=e.nativeEvent.offsetY- this.state.mouseY
 
            if(rects.length>=0){
              rects.push({x: this.state.mouseX, y: this.state.mouseY, w: width, h: height,label:this.state.discoveryName})
               rectangleIndex=rects.length-1
              this.savelabelName()
              this.drawRectangle(rects)
            }
            
          }

          this.setState({
            takeCoordinate:false,
            shallMouseMove:false
          })
     }
     componentDidMount(){
       this.setState(previousStateValue,()=>{
         console.log(previousStateValue)
         if(!_.isEmpty(previousStateValue)&&previousStateValue.imageArray.length>0){
          this.loadImage(this.state.imageIndex)
         }
       })

      document.body.addEventListener("keydown", this.handleKeyDown,false);
     }
     handleKeyDown = (event) => {
       let input= event.srcElement.type=="text"?true:false
       if(event&&this.state.imageArray.length>0){
        if(event.key === '5'&&!input) {
           this.saveImage()
        } else if( event.key === '6'&&!input) {
          this.imageHandler(">>")
       } else if(event.key === '4'&&!input) {
        this.imageHandler("<<")
        } else if(event.key === '8'&&!input) {
          this.deleteRectangle()
          }
       }
  }
  componentWillUnmount() {
    previousStateValue={...this.state}
     document.body.removeEventListener('keydown', this.handleKeyDown,false)
}
    handleMouseClick=(e)=>{
          function collides(rectss, x, y) {
               var isCollision = false;
               let self =this
               for (var i = 0, len = rectss.length; i < len; i++) {
                   var left = rectss[i].x, right = rectss[i].x+rectss[i].w;
                   var top = rectss[i].y, bottom = rectss[i].y+rectss[i].h;
                   if (right >= x
                       && left <= x
                       && bottom >= y
                       && top <= y ) {
                       isCollision = rectss[i];
                         rectangleIndex=i
                   }
               }
               return isCollision;
             }
              var rect = collides(rects, e.nativeEvent.offsetX, e.nativeEvent.offsetY);

             if (rect) {
               console.log(rect)
               if(rect.label!=""){
                this.setState({discoveryName:rect.label,getProjectName:false,showPopup:true, createDiscovery:true})
                
               }else{
                if(this.state.projectName===""){
                this.setState({getProjectName:true,showPopup:true, createDiscovery:false})

                 }else{
                this.setState({getProjectName:false,showPopup:true, createDiscovery:true})
                 
                 }
               }
 
                return this.saveImageWithCoordinates(rect.x ,rect.y)  
             } else {
                 console.log('no collision');
             }
           this.setState({
             mouseX:e.nativeEvent.offsetX,//e.screenX,
             mouseY:e.nativeEvent.offsetY,//e.screenY,
             shallMouseMove:true
           })
     }
          saveImageWithCoordinates=(XValue,YValue)=>{
          coordinate={}
          rects.forEach(element=>{
          if(element.x===XValue&&element.y===YValue){
          coordinate= element
          
        }
      })
     
       }
       // draw rectangle line 
         handleMouseEvent=(e)=>{
          if(this.state.shallMouseMove==true){ 
           this.setState({takeCoordinate:true})
           let width=e.nativeEvent.offsetX- this.state.mouseX;
           let height=e.nativeEvent.offsetY- this.state.mouseY
            // console.log(width,height)
           contexts.clearRect(0,0,this.canvasElement.width,this.canvasElement.height); 
           contexts.drawImage(image, 0, 0,image.width,image.height,0,0,640,480);
 
           contexts.lineWidth = "4";
           contexts.strokeStyle = 'red';
           contexts.strokeRect(this.state.mouseX, this.state.mouseY, width,height);
         }
       }

       saveImage=()=>{
         let saveImageBoolean=true
        if(this.state.imageArray[this.state.imageIndex]&&this.state.imageArray[this.state.imageIndex].discoveryArray.length>0){
          this.state.imageArray[this.state.imageIndex].discoveryArray.forEach((element,index) => {
            if(element.label===""||!element.label){
              console.log(element.label)
             saveImageBoolean=false
             toast.error(index+1+" discovery name is missing", {
               position: toast.POSITION.TOP_RIGHT
           });
            }
          });
        }else{
          saveImageBoolean="false"
          toast.warn("Please add discovery", {
            position: toast.POSITION.TOP_RIGHT
        });
        }

         if(saveImageBoolean===true){
          var formData = new FormData();
          formData.append("reactangle",rects)
          formData.append("image",this.state.imageBlob)
          formData.append("image_save", './')
          formData.append("image_name",this.state.imageArray[this.state.imageIndex].name)
          formData.append("folder_name",this.state.imageArray[this.state.imageIndex].webkitRelativePath.split('/')[0])
          formData.append("image_width", 640)
          formData.append("image_heigth",480)
          formData.append("project_name",this.state.projectName)
          formData.append("bbox",JSON.stringify(rects))
         
          if(this.state.imageBlob!=""){
            mlAction.saveTrainedImage(formData).then(response=>{
              if(response.payload.data.status===200){
                this.state.imageArray[this.state.imageIndex].discoveryArray.forEach(element => {
                  element.labelColor="Green"
                 });
                 rects=this.state.imageArray[this.state.imageIndex].discoveryArray
                this.drawRectangle(this.state.imageArray[this.state.imageIndex].discoveryArray)
                toast.success("Image saved Successfully", {
                  position: toast.POSITION.TOP_RIGHT
              });
              this.getSavedImages()
              }else{
                toast.error(response.payload.data.result, {
                  position: toast.POSITION.TOP_RIGHT
              });
              }
               
            }).catch(e=>{
               toast.error(e.toString(), {
                  position: toast.POSITION.TOP_RIGHT
              });
              console.log(e)
            })
           
          }
  
         }
        
       }

       onClose=()=>{
        this.setState({getProjectName:false,showPopup:false})
       }
// save discovery names
       savelabelName=()=>{

         if(this.state.searchDiscoveries.length===0){
            this.setState({searchDiscoveries:[...this.state.searchDiscoveries,this.state.discoveryName]})
         }
         if(rects.length>0){
          rects[rectangleIndex].label=this.state.discoveryName
          let imageWithRect=this.state.imageArray[this.state.imageIndex]
          let checkExist=_.findIndex(imageWithRect.discoveryArray,rects[rectangleIndex]);
  
          if(checkExist<0){
           imageWithRect.discoveryArray.push(rects[rectangleIndex])
          }else{
           imageWithRect.discoveryArray[checkExist]=rects[rectangleIndex]
          }
         let labelName=[...this.state.discoveryNames]
         if(!_.includes(labelName, this.state.discoveryName)){
           labelName.push(this.state.discoveryName)
         }
 
          this.setState({discoveryNames:labelName})
          this.drawRectangle(rects)
         }
       
       }
       deleteRectangle=()=>{
       if(this.state.imageArray[this.state.imageIndex]&&this.state.imageArray[this.state.imageIndex].discoveryArray.length>0){
        let imageWithRect=this.state.imageArray[this.state.imageIndex]
        let checkExist=_.findIndex(imageWithRect.discoveryArray,rects[rectangleIndex]);
        if(checkExist>=0){
          imageWithRect.discoveryArray.splice(checkExist,1)
          rects=imageWithRect.discoveryArray
          rectangleIndex=rects.length-1
       }
        if(imageWithRect.discoveryArray.length>0){
         this.drawRectangle(imageWithRect.discoveryArray)
       }else{
         rects=[]
         // make empty
         contexts.clearRect(0,0,this.canvasElement.width,this.canvasElement.height); 
         contexts.drawImage(image, 0, 0,image.width,image.height,0,0,640,480);

       }
       }else{
        toast.error("No discovery found!", {
          position: toast.POSITION.TOP_RIGHT
      });
       }
      
       }
    drawRectangle=(imageArray)=>{
      // console.log(imageArray)
      contexts.clearRect(0,0,this.canvasElement.width,this.canvasElement.height); 
      contexts.drawImage(image, 0, 0,image.width,image.height,0,0,640,480);
      contexts.lineWidth = "4";
        contexts.font ="bold 24px verdana, sans-serif "
       imageArray.forEach((element,index)=>{
        contexts.strokeStyle=element.labelColor?element.labelColor: 'red'
        contexts.fillStyle=element.labelColor?element.labelColor: 'red'

         contexts.rect(element.x, element.y, element.w, element.h)
        contexts.strokeRect(element.x, element.y, element.w, element.h)
        contexts.beginPath();
        contexts.fillText(element.label, element.x-3, element.y-5);
      })
    }
    clearRectangle=()=>{
      if(this.state.imageArray[this.state.imageIndex]&&this.state.imageArray[this.state.imageIndex].discoveryArray.length>0){
        contexts.clearRect(0,0,this.canvasElement.width,this.canvasElement.height); 
        contexts.drawImage(image, 0, 0,image.width,image.height,0,0,640,480);
        this.state.imageArray[this.state.imageIndex].discoveryArray=[]
        rects=[]
      }
     
    }
    
    getannotatedImages=(frame)=>{
       let index  =_.findIndex(this.state.imageArray,{name:frame.file_name});
       this.loadImage(index)
    }

    saveProjectName=()=>{

      if(this.state.projectName!=""){

            this.setState({showPopup:false, getProjectName:false})
            var formData = new FormData();
            formData.append("project_name",this.state.projectName)
            formData.append("folderName",this.state.pickedFolderName)
            
            mlAction.getannotatedImages(formData).then(response=>{

              if(response.status===200){
               annotatedImages=response.result.reverse().slice(0 * this.state.limit, (0 + 1) * this.state.limit);
 
                  this.setState({annotatedImages:response.result.reverse(),pageNumber:1})
                        if(array.length>0){
                         
                           this.setState({imageArray:array},()=>{
                           this.loadImage(0)
                          })
                           toast.info(response.result.length + " images annotated in this folder", {
                              position: toast.POSITION.TOP_RIGHT
                          });
                          }else{
                            toast.error("No image found", {
                              position: toast.POSITION.TOP_RIGHT
                          });
                          
                          }
                
                    }else{
                      toast.warn(response.status+" "+response.result, {
                        position: toast.POSITION.TOP_RIGHT
                    });
                        this.setState({annotatedImages:[]})
                    }
                }).catch(e=>{
                console.log(e,e.toString())
                 toast.error(e.toString(), {
                              position: toast.POSITION.TOP_RIGHT
                          })})
        }else{
         toast.error("Please provide project name", {
            position: toast.POSITION.TOP_RIGHT
        });
      }
    }
    getSavedImages=()=>{
      var formData = new FormData();
      formData.append("project_name",this.state.projectName)
      formData.append("folderName",this.state.pickedFolderName)
      
      mlAction.getannotatedImages(formData).then(response=>{

        if(response.status===200){
         annotatedImages=response.result.reverse().slice(0 * this.state.limit, (0 + 1) * this.state.limit);

            this.setState({annotatedImages:response.result.reverse()})
                  if(array.length>0){
                   
                     this.setState({imageArray:array})
                    //  toast.info(response.result.length + " images annotated in this folder", {
                    //     position: toast.POSITION.TOP_RIGHT
                    // });
                    }else{
                    //   toast.error("No image found", {
                    //     position: toast.POSITION.TOP_RIGHT
                    // });
                    
                    }
          
              }else{
                toast.warn(response.status+" "+response.result, {
                  position: toast.POSITION.TOP_RIGHT
              });
                  this.setState({annotatedImages:[]})
              }
          }).catch(e=>{
          console.log(e,e.toString())
           toast.error(e.toString(), {
                        position: toast.POSITION.TOP_RIGHT
                    })})

    }
    handlePageChange=(pageNumber)=>{
        annotatedImages=this.state.annotatedImages.slice(pageNumber * this.state.limit, (pageNumber + 1) * this.state.limit);
        this.setState({getProjectName:false})
     }
  render() {
    console.log(this.state.imageBlob)
   return (
          <div>
               {this.state.showPopup===true?
                 <Popup
                 className="custom-ui"
                  open={this.state.showPopup}
                  
                 onClose={e=>this.setState({showPopup:false})}
                 >
                {this.state.getProjectName===true?<div className="row scan-body">
               <div className=" col-md-8  flot-left'">
               <div className='text-dark '>
                  <div>
                        <label className="title">Project Name :  </label>
                      <input type="text" className="description truncate"  value={this.state.projectName} name="projectName" onChange={e=>this.handleChange(e)} onKeyUp={e=>this.handleChange(e)}  /><br/>
                   </div> <br/>
                  <button className="btn btn-success mr-3" onClick={e => {
                        this.saveProjectName();}
                       }>
                   Save
                  </button>
                  <button className="btn btn-danger mr-3" onClick={e=>{this.onClose()}}>No</button>
               </div>
          </div>
            </div>:<div className="row scan-body">
               <div className=" col-md-8  flot-left'">
               <div className='text-dark text-center'>
                  <h1>Save Rectangle</h1>
                  <div>
                        <label className="title">Discovery Name :  </label>
                      <input type="text" className="description truncate"  value={this.state.discoveryName} name="discoveryName" onChange={e=>this.handleChange(e)} onKeyUp={e=>this.handleChange(e)}  /><br/>
                   </div> <br/>
                  <button className="btn btn-success  mr-3" onClick={() => {
                         this.savelabelName(); 
                        this.onClose();}
                       }>
                   Add
                  </button>
                  <button className="btn btn-danger mr-3" onClick={() => {
                         this.deleteRectangle (); 
                        this.onClose();}
                       }>
                   Delete
                  </button>
                  <button className="btn btn-danger mr-3" onClick={e=>{this.onClose()}}>No</button>
               </div>
          </div>

          <div className="  col-md-4  flot-right">
          <section className="card mt-3 mb-3">
                          <header className="card-header">
                                <h2 className="card-title">Discovery Names</h2>
                            </header>
                            <div className="card-body">
                          <ul className="simple-bullet-list mb-3 c-pointer">
                            {this.state.discoveryName===""?
                            this.state.discoveryNames.map((name,index) =>
                            <li key={index} className="">
                            <span className="title"  onClick={()=>{this.setState({discoveryName:name})}} >{name}</span>
                         </li>     
                          )
                              :this.state.searchDiscoveries.map((name,index) =>
                              <li key={index} className="">
                              <span className="title"  onClick={()=>{this.setState({discoveryName:name})}} >{name}</span>
                           </li>     
                            )}
                                 
                                </ul> 
                         </div>
                         
                </section> 
          </div>

            </div>}
                 
               </Popup>:null}

            <section role="main" className="content-body">
            <header className={'page-header ' + (this.state.showMenu ? '' : 'header-left')}>
            <h2>Training model</h2>
            </header>
              
              <div className="row scan-body">
               <div className="col-xl-9 col-lg-8 col-md-8 col-sm-8 mb-4 mb-xl-0 mt-sm-3 flex-result">
               <form>
          <div class="custom-file">
            <input type="file" class="custom-file-input" id="customFile"  onChange={e => this.pickFolder(e)} webkitdirectory="" directory value={this.state.file} />
            <label class="custom-file-label" for="customFile">Choose file</label>
          </div>
        </form>
             
                       {this.state.imageArray.length>0?
                        <div className="form-group row mt-2">
                        <div className="col-md-12  text-white">
                        <button className="btn btn-primary mr-1" type="button" id="submit" onClick={e=>this.imageHandler('<<')} >{'<<'}</button>
                        <button className="btn btn-primary mr-1" type="button" id="submit" onClick={e=>this.imageHandler(">>")} >{">>"}</button>
                        <button className="btn btn-primary mr-1" type="button" id="submit" onClick={e=>this.saveImage()} >{"Save Image"}</button>
                        <button className="text-right btn btn-danger" type="button" id="submit" onClick={e=>this.clearRectangle()} >{"Clear Rectangle"}</button>

                        {this.state.imageArray.length>0?
                        <span className="ml-2">
                          <span className="title text-dark" >{this.state.imageIndex+1} of {this.state.imageArray.length} Images  </span> 
                          <span className="text-dark ml-3" >
                           Go to 
                          <input type="text" className="description truncate"  value={this.state.goTo} name="goTo" onChange={e=>this.handleChange(e)} onKeyUp={e=>this.handleChange(e)}  />
                          </span>
                          <br/>
 <br></br>
                        {this.state.imageArray[this.state.imageIndex]?<span className="text-dark h5">{this.state.imageArray[this.state.imageIndex].name}</span>:null}
                        </span>
                         :null} 
                        </div>
                        
                   </div>
                      :null}
                                   
                  {this.state.imageArray.length>0?
                  <section className="card">
                  <div className="card-body">
                      <div className="form-data row">
                          <div className="col-12">
                              {this.state.imageArray.length>0? <canvas id="canvas"
                              onMouseUp={(e)=>this.handleMouseRelease(e)} 
                              onMouseDown={(e)=>this.handleMouseClick(e)}
                              onMouseMove={(e,canvas)=>this.handleMouseEvent(e,canvas)}
                              className={"img-fluid"+this.state.imageBlob!==""?"embed-responsive-item":"embed-responsive-item d-none"}
                              width="640" height="480" 
                              ref={(input) => this.canvasElement = input} />:null}
                          </div> 
                          
                      </div>
                  </div>
                    </section>:null}
                
          </div>
         

           {this.state.annotatedImages.length>0? <div className="col-xl-3 col-lg-4 col-md-4 col-sm-4 mb-4 mb-xl-0 mt-sm-3 flex-result my-custom-scrollbar">
          <section className="card">
                  <div className="card-body">
                      <div className="form-data row">

                      <span className="text-dark h5 text-left ml-4" >Annotated Images</span>

                          <div className="text-dark">
                          {annotatedImages.map((name,index) =>
                            <ul key={index} onClick={e=>{this.getannotatedImages(name)}}>
                              <li >
                                <span className="c-pointer">{name.file_name}</span>
                              </li>
                              </ul>
                                //  <img key={index}className="mt-1 ml-2 mr-2 md -3"src={URL.createObjectURL(image)} width="170" height="150" ></img> 
                             )} 
                             <div className=" h5 pull-right">
                             <ReactPaginate
                                previousLabel={'previous'}
                                nextLabel={'next'}
                                breakLabel={'...'}
                                breakClassName={'break-me'}
                                pageCount={this.state.annotatedImages.length/this.state.limit}
                                marginPagesDisplayed={2}
                                pageRangeDisplayed={3}
                                onPageChange={e=>this.handlePageChange(e.selected)}
                                containerClassName={'pagination'}
                                subContainerClassName={'pages pagination'}
                                activeClassName={'active'}
                              />
                             </div>
                             
 
                            </div> 
                      </div>
                  </div>
                    </section>
          </div>:null}
      </div>
  </section>
  <ToastContainer/>
</div>

			);
  }
}
const mapStateToProps = (state) => {
  
}
const mapDispatchToProps = (dispatch) => {
  return {
     
  }
}
export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(TrainingModel)
  ;