import React, { Component } from 'react';
import { withRouter} from 'react-router-dom';
 import { connect } from "react-redux";
 import * as userAction from "../actions/user.action"
 import * as types from "../actions/types"
import _ from 'lodash'
import Popup from "reactjs-popup";
import ReactPaginate from 'react-paginate';
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";
import userImg from '../assets/images/user.jpg';

import { ToastContainer, toast } from 'react-toastify';
// import Pagination from "react-js-pagination";
// require("bootstrap/less/bootstrap.less");
import { Pagination } from 'react-bootstrap';

 class TranslationSession extends Component {
    constructor(props) {
        super();
        this.state = {
			pageLimit:5,
			
			newPatient:{},
			showPopup:false,
			patientSearch:"",
			patient_list:{pagination: {page: 1, pageSize: 10, rowCount: 3, pageCount: 1},patients:[]},
               }
      }
      componentDidMount(){
		  console.log(this.props.scanReducer)
       if(localStorage.getItem('intelliscope-token')){
 		  this.getAllPatients(1,this.state.pageLimit,'')
	  }else{
		this.HistoryPush("/")
	  }
	  
     }
    componentWillReceiveProps(nextProps){
 	 if(nextProps.error){

	 }else{
		// patient_list=!_.isEmpty(nextProps.patient_list) ?nextProps.patient_list:patient_list
		//  this.setState({patient_list:nextProps.patient_list})
	 }
	 
    }
   
	HistoryPush=(path,patient)=>{
		if(path==="/patient_details"){
			this.props.patientId(patient)
		}
		this.props.history.push(path)
		this.setState({ displayMenu: false})
	  
	  }
	  searchPatient=(e)=>{
		console.log(e.target.name,e.target.value)
		  this.setState({[e.target.name]:e.target.value},()=>{
			 
		  })
		  this.getAllPatients(1,this.state.pageLimit,e.target.value)
 	  }
	  closeHandle=()=>{
        this.setState({showPopup:!this.state.showPopup,
           discoveryDetail:"",
           discoveryName:""
       })
	   }
	   handleCreatePatient=(e)=>{
	let newPatient=this.state.newPatient
	newPatient[e.target.name]=e.target.value
	this.setState({newPatient})

	   }
	   createPatient=()=>{
		    console.log(this.state.newPatient)
		 if(this.state.newPatient.phoneNumber&&this.state.newPatient.phoneNumber.toString().length>10){ 
			toast.error("phone number should not have more then 10 digits", {
				position: toast.POSITION.TOP_RIGHT
				});
			   
					}else{
		   userAction.createPatient(this.state.newPatient).then(res=>{
			   console.log(res,this.state.newPatient)
if(res.status===200){
	// console.log(res.data.message)
	toast.success(res.data.status.message?res.data.status.message:"something went wrong", {
		position: toast.POSITION.TOP_RIGHT
	});
	this.getAllPatients(1,this.state.pageLimit,'')
	this.setState({showPopup:false,newPatient:{}})
}else{
	toast.error(res.data.message, {
		position: toast.POSITION.TOP_RIGHT
	});
}
		   })
		}
	}
		handlePageChange = (pageNumber) => {	
			 
 				this.setState({
					pageNumber: pageNumber,
				})

				this.getAllPatients(pageNumber,this.state.pageLimit,'')
			
		}

		deletePatient=(patient)=>{
			confirmAlert({
				customUI: ({ onClose }) => {
				  return (
					<div className='custom-ui'>
					  <h1>Are you sure?</h1>
					  <p>You want to delete this patient?</p>
					  <button className="btn btn-success" onClick={() => {
						  userAction.deleteUser(patient.id).then(result=>{
							this.getAllPatients(this.state.pageNumber,this.state.pageLimit,'')
							});onClose();}
 						  }>
						Yes, Delete it!
					  </button>
					  <button className="btn btn-danger" onClick={onClose}>No</button>
					</div>
				  );
				}
			  });
        
		} 
		checkLog=(checkLog)=>{
			console.log(checkLog)
		}   
getAllPatients=(pageNumber,limit,search)=>{
	this.props.getAllPatients({pageNumber:pageNumber,limit:limit,search:search}).then(res=>{
		console.log(res,this.props.patient_list)
		this.setState({patient_list:this.props.patient_list},()=>{
			console.log(this.state.patient_list)
		})
	})

}  	 
    render() {
		let {patient_list}= {...this.state}
console.log(patient_list)
		let active = 2;
		let items = [];
		for (let number = 1; number <= 5; number++) {
		  items.push(
			<Pagination.Item key={number} active={number === active}>
			  {number}
			</Pagination.Item>,
		  );
		}
		console.log()

          return (
            <div>

				 {this.state.showPopup===true?<Popup
                   open={this.state.showPopup}
                   closeOnDocumentClick={false}
                   onClose={e=>this.setState({showPopup:!this.state.showPopup})}
                 >
                  <div className="model-close p-3">
                 		<h3 className="mt0 mb0"><b> Create Patient</b>
                       <a className="close" onClick={e=>this.closeHandle()}>
                        &times;
                        </a></h3>
                   </div>
                  
					<hr className="mt0 mb0"/>
  				   {/* {this.state.capturedImage===""? <Webcam className="take-picture"
                                   audio={false}   ref={this.setRef}   
                                  screenshotFormat="image/jpeg" onUserMediaError={e=>this.onUserMediaError(e)}/> : */}
                                  {/* <img src={userImg}  className="round-img"  alt="User Image" /> */}
							{/* //  } */}
  			       <form className="p-3">
				   
						<div className="form-row">
							<div className="form-group col-md-6">
								<label htmlFor="inputCity">First Name</label>
								<input type="text" name="firstname" value={this.state.newPatient.firstname?this.state.newPatient.firstname:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control"   />
							</div>
							 
							 <div className="form-group col-md-6">
								<label htmlFor="inputState">Last Name</label>
								<input type="text" name="lastname" value={this.state.newPatient.lastname?this.state.newPatient.lastname:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control"   />
							</div>
							 
						</div>
						<div className="form-row">
							<div className="form-group col-md-6">
								<label htmlFor="inputCity">Gender</label>
								{/* <input type="text" name="gender" value={this.state.newPatient.gender?this.state.newPatient.gender:""} */}
								<select type="text" name="gender" value={this.state.newPatient.gender?this.state.newPatient.gender:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control" > 
									<option selected>Choose...</option>
									<option value="Male">Male</option>
									<option value="Female">Female</option>
									<option value="Other">Other</option>


								</select>
							</div>
							 
							 <div className="form-group col-md-6">
								<label htmlFor="inputDob">DOB</label> <br></br>
 								<DatePicker data-plugin-datepicker   
								          className="form-control w466"
                                                    onChange={(date) =>{
														console.log(new Date(date));
 														this.setState(prevState => ({
															newPatient: {                   
																...prevState.newPatient,     
																dob:new Date(date)// moment(date).format('MM-DD-YYYY')       
															}
														}))
													}} 
													onKeyUp={(date) =>{
														console.log(new Date(date));
 														this.setState(prevState => ({
															newPatient: {                   
																...prevState.newPatient,     
																dob:new Date(date)// moment(date).format('MM-DD-YYYY')       
															}
														}))
													}}
													name="dob" 
													selected={this.state.newPatient.dob?this.state.newPatient.dob:new Date()}    
                                                    dateFormat="MM-dd-yyyy" />
							</div>
							 
						</div>
						<div className="form-group">
							<label htmlFor="inputAddress">Address</label>
							<input type="text" name="address" 
							value={this.state.newPatient.address?this.state.newPatient.address:""} 
							onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}}
							 className="form-control"    />
						</div>
						 
						<div className="form-row">
							<div className="form-group col-md-6">
								<label htmlFor="inputCity">City</label>
								<input type="text" name="city" value={this.state.newPatient.city?this.state.newPatient.city:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control"   />
							</div>
							{/* <div className="form-group col-md-4">
								<label htmlFor="inputState">State</label>
								<select  className="form-control">
									<option selected>Choose...</option>
									<option>...</option>
								</select>
							</div> */}
							 <div className="form-group col-md-4">
								<label htmlFor="inputState">State</label>
								<input type="text" name="state" value={this.state.newPatient.state?this.state.newPatient.state:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control"   />
							</div>
							<div className="form-group col-md-2">
								<label htmlFor="inputZip">Zip</label>
								<input type="text" name="zipcode" value={this.state.newPatient.zipcode?this.state.newPatient.zipcode:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control"  />
							</div>
						</div>
						<div className="form-row">
							<div className="form-group col-md-6">
								<label htmlFor="inputEmail">Email</label>
								<input type="text" name="email" value={this.state.newPatient.email?this.state.newPatient.email:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control"  />
							</div>
							<div className="form-group col-md-6">
								<label htmlFor="inputphone">Phone</label>
								<input type="text" name="phoneNumber" value={this.state.newPatient.phoneNumber?this.state.newPatient.phoneNumber:""} onChange={e=>{this.handleCreatePatient(e)}} onKeyUp={e=>{this.handleCreatePatient(e)}} className="form-control"  />
							</div>
						</div>
						<div className="text-right">
							<button type="button"onClick={e=>this.createPatient()} className="btn btn-primary border-0"><i className="fas fa-save text-white mt-1 mr-1"></i>Save</button>
						</div>
					</form>
                
				 </Popup>:
				 null}
                <section role="main" className="content-body">
					<header className={'page-header ' + (this.state.showMenu ? '' : 'header-left')}>
						<h2>Patient List</h2>
					</header>
				 
					<div className="search-content">
						<div className="search-control-wrapper">
                            <form action="#">
								<div className="form-group">
									<div className="input-group input-group-sm">
										<input type="text" value={this.state.patientSearch} name="patientSearch" 
 										onChange={e=>this.searchPatient(e)} 
										onKeyUp={e=>this.searchPatient(e)} 
										className="form-control form-control-md" 
										placeholder="Search..." />
										<span className="input-group-append text-white">
											<a className="btn btn-primary p-2 btn-sm "><i className="fas fa-search"></i> Search</a>
                                            <a className="btn btn-success p-2 btn-sm" onClick={e=>{this.setState({showPopup:true})}}><i className="fas fa-plus"></i> New</a>
                                        </span>
									</div>
								</div>
							</form>
						</div>
                        <div className="row mt-5 ml-1">
                            <div className="col-12">
							{patient_list.patients&&patient_list.patients.length>0?
							<p className="total-results text-muted">{patient_list.patients?`Showing ${patient_list.patients.length} of ${this.state.patient_list.pagination.rowCount}`:null} results</p>
							:null
							}
                            </div>
                        </div>
                        
						<div className="row">
							<div className="col-12 mb-3">
								
								<ul className="list-unstyled search-results-list w-100 col-12">
								{ patient_list.patients?patient_list.patients.map((patient,index)=>
									<li key={index} className="p-0 m-0">
										<p className="result-type patient-icon "><span className="badge badge-primary">{patient.role.roleName}</span> <i className="fa fa-trash text-danger mt-4 fs fa-lg" onClick={e=>this.deletePatient(patient)} aria-hidden="true"></i></p>
										<a onClick={(e)=>{this.HistoryPush('/patient_details',patient)}}>
											<div className="result-data pointer ">
												<p className="h4 title text-primary mb-0 ">{patient.firstname} {patient.lastname}</p>
												<p className="description">{patient.email} </p>
											</div>
										</a>
									</li>
								):null}
									
								</ul>
								<div className="float-right p-3 h4">
							
							<ReactPaginate
								previousLabel={'previous'}
								nextLabel={'next'}
								breakLabel={'...'}
 								pageCount={patient_list.pagination.pageCount}
								marginPagesDisplayed={3}
								pageRangeDisplayed={3}
 								onPageChange={e=>this.handlePageChange(e.selected+1)}
								containerClassName={'pagination'}
								subContainerClassName={'pages pagination'}
								activeClassName={'active'}
							/>
							
							</div>
							</div>
						</div>

					</div>
                    </section>
					
					<ToastContainer/>
            </div>
        )
    }
}
 const mapStateToProps = (state) => {
     return {
		patient_list:state.patientReducer.patients,
		scanReducer:state.scanReducer
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
		 getAllPatients:(obj)=>{return dispatch(userAction.getAllPatients(obj))},
		 patientId:(obj)=>{dispatch({type:types.PATIENT_ID,payload:obj})}
    }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TranslationSession));