 
import  * as types from "../actions/types";

const initialState={
    discoveryList:{},
 errors:null
};
    

const discoverynReducer = (state = initialState, action) => {
 
switch(action.type){
    case types.GET_DISCOVERY_LIST:{
         return {...state,discoveryList:action.payload}
    }
    default:{
        return {...state,errors:null};
    }
}
}

export default discoverynReducer;