import React, { Component } from 'react';
import userImg from '../assets/images/user.jpg';
import { withRouter} from 'react-router-dom';
 import { connect } from "react-redux";
 import {Tab,Tabs,TabList,TabPanel} from 'react-tabs';
 import * as userAction from "../actions/user.action";
import * as scanAction from "../actions/scan.action";
import Webcam from "react-webcam";
import * as types from "../actions/types"
import Popup from "reactjs-popup";
import _ from 'lodash'
import moment from "moment";
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css';
import { ToastContainer, toast } from 'react-toastify';
import { baseUrl } from '../config';
 import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

let recentScans=[]

 class ScanConponent extends Component {
    constructor(props) {
        super();
        this.state = {
            statusOption:['Open','Closed','Attention'],
            newScan:{},
            openType:"",
            recentScans:{},
            editMode:false,
            country: '', region: '',
            patient:{},
            showPopup:false,
            newPrescriptionPopup:false,
            searchScan:"",
            prescriptionObj:{},
            prescription:{pagination: {page: 1, pageSize: 10, rowCount: 3, pageCount: 1},result:[]},
            patientAllergyList:{result:[]},
            allergyList:{result:[]},
            newAllergy:"",
            takePhoto:false,
            profileImage:"",
            webCamError:"",
            capturedImage:""

                 }
      }
      componentDidMount(){
       if(this.props.patientId===0){
         this.HistoryPush("/patient_management")
            }else{
                console.log(this.props.patientId,this.props.patientDetail)
                  this.setState({patient:this.props.patientDetail},()=>{
                     this.props.getPatientDetails(this.props.patientId)
                     this.props.getRecentScans({pageNumber:1,limit:10,search:''},this.state.patient.id)
                     this.props.getPrescription({pageNumber:1,limit:10,search:''},this.state.patient.id)
                     this.showpatientAllergy()
                    //  this.updateProfilePic()
                  })
            }
     }
    componentWillReceiveProps(nextProps){
          if(nextProps.patientId===0){
             this.HistoryPush("/patient_management")
        }else{
            console.log(nextProps.recentScans)
            recentScans=nextProps.recentScans.result?nextProps.recentScans.result:[]
            this.setState({prescription:nextProps.patinetPriscription,
                patient:nextProps.patientDetail,
                recentScans:nextProps.recentScans})
        }
    }
    
    HistoryPush=(path)=>{
    this.props.history.push(path)
    
      }
      handleatientDetailChange=(e)=>{
         let patient=this.state.patient
        patient[e.target.name]=e.target.value
        this.setState({patientDetail:patient})
      }
      selectRegion (val) {
        let patient=this.state.patient
        patient["state"]=val
        this.setState({patientDetail:patient})

 
      }
      selectCountry (val) {
        let patient=this.state.patient
        patient["country"]=val
        this.setState({patientDetail:patient})
      }
      capture=()=>{
        if(this.state.webCamError===""){
             this.setState({capturedImage:this.webcam.getScreenshot()})
           }else{
            toast.error(this.state.webCamError, {
                position: toast.POSITION.TOP_RIGHT
            });  
        }
      }
      updateProfilePic=()=>{
        let base64={profileImg:this.state.capturedImage}
        console.log(this.state.patient.id,base64,this.webcam,this.state.webCamError)
        
        
        userAction.updateUserProfile(this.state.patient.id,base64).then(Response=>{
            console.log(Response)
            if(Response.statusCode===200){
                this.props.getPatientDetails(this.props.patientId)
                this.setState({takePhoto:false,editMode:false,capturedImage:""})
            }else{
                toast.error(Response.message?Response.message:"Something went wrong", {
                   position: toast.POSITION.TOP_RIGHT
               });  
           }
        })
      }
      setRef = webcam => {
        this.webcam = webcam;
    };
      updateProfile=()=>{
        let patient={
            firstname:this.state.patient.firstname,
            lastname:this.state.patient.lastname,
            address:this.state.patient.address,
            phoneNumber:this.state.patient.phoneNumber,
            city:this.state.patient.city,
            zipcode:parseInt(this.state.patient.zipcode),
            state:this.state.patient.state,
            dob:this.state.patient.dob,
            gender:this.state.patient.gender

         }
         if(patient.phoneNumber.toString().length>10){ 
            toast.error("phone number should not have more then 10 digits", {
                position: toast.POSITION.TOP_RIGHT
                });
   
        }else{
            console.log(patient)
            userAction.updatePatient(this.state.patient.id,patient).then(res=>{
                 if(res.statusCode===200){
                    this.props.getPatientDetails(this.props.patientId)
                    this.setState({editMode:false})
                toast.success("Updated", {
               position: toast.POSITION.TOP_RIGHT
               });
                }else{
                  toast.error("Something went wrong", {
                   position: toast.POSITION.TOP_RIGHT
               });
                }
            })
        }
        
       }

       closeHandle=()=>{
        this.setState({showPopup:false,takePhoto:false,newPrescriptionPopup:false,
            showAllallergy:false,
          newScan:{}
       })
       }
       createScan=()=>{
          scanAction.createNewScan(this.state.newScan).then(result=>{
             this.setState({showPopup:false,newScan:{}})
            this.props.getRecentScans({pageNumber:1,limit:10,search:''},this.state.patient.id)
          })
       }
       handleChange=(e)=>{
        let newScan=this.state.newScan
        newScan.patientId=this.state.patient.id
        newScan[e.target.name]=e.target.value
        this.setState({newScan})
       }
       handleChangeAction=(e)=>{
           this.setState({[e.target.name]:e.target.value})
       }
       
    searchScan=(e)=>{
        this.setState({[e.target.name]:e.target.value})
        this.props.getRecentScans({pageNumber:1,limit:10,search:e.target.value},this.state.patient.id)
      }
      deleteScan=(scanObj)=>{
          confirmAlert({
            customUI: ({ onClose }) => {
              return (
                <div className='custom-ui'>
                  <h1>Are you sure?</h1>
                  <p>You want to delete this Scan?</p>
                  <button className="btn btn-success" onClick={() => {
                       scanAction.deleteScan(scanObj.id).then(result=>{
                        this.props.getRecentScans({pageNumber:1,limit:10,search:''},this.state.patient.id)
                      });onClose();}
                       }>
                    Yes, Delete it!
                  </button>
                  <button className="btn btn-danger" onClick={onClose}>No</button>
                </div>
              );
            }
          });
    
        //   if(window.confirm("You want to delete scan ")){
        //     scanAction.deleteScan(scanObj.id).then(result=>{
        //         this.props.getRecentScans({pageNumber:1,limit:10,search:''},this.state.patient.id)
    
        //       })
        //   }
       }
       handleCreatePrescription=(e)=>{
        let prescriptionObj=this.state.prescriptionObj
        prescriptionObj.userId=this.state.patient.id
        prescriptionObj[e.target.name]=e.target.value
        this.setState({prescriptionObj})
       }
    
        actionPrescription=(obj,type)=>{
           if(type==="update"){
            userAction.updatePrescription( this.state.prescriptionObj.id,this.state.prescriptionObj).then(result=>{
                this.getPrescription()
              })
           }else if(type==='delete'){
                  confirmAlert({
                    customUI: ({ onClose }) => {
                      return (
                        <div className='custom-ui'>
                          <h1>Are you sure?</h1>
                          <p>You want to delete this Prescription?</p>
                          <button className="btn btn-success" onClick={() => {
                             userAction.deletePrescription(obj.id).then(result=>{
                                toast.success("Successfully deleted", {
                                    position: toast.POSITION.TOP_RIGHT
                                });
                                this.getPrescription()
                              });onClose();}
                               }>
                            Yes, Delete it!
                          </button>
                          <button className="btn btn-danger" onClick={onClose}>No</button>
                        </div>
                      );
                    }
                  });
           
           }else if(type==='save'){


            userAction.createPrescription(this.state.prescriptionObj).then(res=>{
            //    console.log(res)
                if(res.status===200){
                    // console.log(res.data.message)
                    toast.success("New prescription created", {
                        position: toast.POSITION.TOP_RIGHT
                    });
                    this.setState({showPopup:false,prescriptionObj:{},newPrescriptionPopup:false})
                this.getPrescription()
                }else{
                    toast.error(res.message, {
                        position: toast.POSITION.TOP_RIGHT
                    });
                }
               
            })

           }
           this.setState({showPopup:false})

       }
       getPrescription=()=>{
        this.props.getPrescription({pageNumber:1,limit:10,search:''},this.state.patient.id)
       }
       showpatientAllergy=()=>{
        userAction.getAllAllergy(this.state.patient.id).then(allergy=>{
            if(allergy){
                
                if(allergy.result.length>0){
                    this.setState({showpatientAllergy:true,patientAllergyList:allergy},()=>{
                    })
                }else{
                    this.setState({showpatientAllergy:false,patientAllergyList:{result:[]}})
    
                }
            }
            
        })
       }
 
    //    showAllallergy=()=>{
    //     userAction.getPatientAllergy(this.state.patient.id).then(allergy=>{
    //         let tempArray=[]
    //         this.state.patientAllergyList.result.forEach(element => {
    //             // allergy.result= _.remove(allergy.result,element.allergy)
    //             if(!_.isEmpty(element.allergy)){
    //                 _.remove(allergy.result,element.allergy)

    //             }
    //             tempArray.push(element.allergy)
    //   });
    //         this.setState({allergyList:allergy})
    //      })
         

    //    }
       allergyAction=(obj)=>{
           
           userAction.deletePatientAllergy(obj).then(result=>{
               console.log(result)
                if(result){
                    if(result.statusCode===200){
                        toast.success(result.data.message, {
                            position: toast.POSITION.TOP_RIGHT
                        });
                        this.showpatientAllergy()
                    }else{
                         toast.error(result.data.message, {
                            position: toast.POSITION.TOP_RIGHT
                        });  
                    }
                }else{
                    toast.error("Something went wrong", {
                        position: toast.POSITION.TOP_RIGHT
                    }); 
                }
                })
       
       }
       createAllergy=()=>{
           let obj={
                allergy_name:this.state.newAllergy,
                patient_id:this.state.patient.id
           }
        userAction.createAllergy(obj).then(result=>{
            if(result.statusCode===200){
                toast.success(result.data.message, {
                    position: toast.POSITION.TOP_RIGHT
                });
                this.setState({showAllallergy:false,createTextField:false,newAllergy:""})
                this.showpatientAllergy()
            }
})
       }
       onChangeFile(e){
        var file = e.target.files[0];
        console.log(file);

    }
    onUserMediaError = (event) => {
        
        this.setState({
          webCamError:"Cam not found"
       })
   }
   openWebCam=()=>{
    if(this.state.webCamError===""){
        this.setState({takePhoto:true})
      }else{
       toast.error(this.state.webCamError, {
           position: toast.POSITION.TOP_RIGHT
       });  
   }
   }
     render() {
        
            return (
            <div>
              
              <Popup
                   open={this.state.takePhoto}
                   closeOnDocumentClick={false}
                   onClose={e=>this.setState({takePhoto:false})}
                 >
                 <div className="model-close p-3">
                    <h3 className="mt0 mb0"><b>Update Profile picture</b>
                       <a className="close" onClick={e=>this.closeHandle()}>
                        &times;
                        </a></h3>
                   </div>
                   <hr className="mt0 mb0"/>
                   <form className="p-3">
                       <div className="form-row">
                           <div className="col-md-12" >
                               <span className="text-center">
                                    {/* <img src={this.state.patient.profileImg?baseUrl+this.state.patient.profileImg:userImg}  className="rounded img-fluid" alt="User Image" />
                                  <div className="mt-2">
                                  <button type='button' className="btn btn-primary col-md-4 offset-1" >Take Photo</button>
                                   <button type='button' className="btn btn-primary col-md-4  ml-5" >Choose File</button>
                                  </div> */}
                                   {this.state.capturedImage===""? <Webcam className="take-picture"
                                   audio={false}   ref={this.setRef}   
                                  screenshotFormat="image/jpeg" onUserMediaError={e=>this.onUserMediaError(e)}/> :
                                  <img src={this.state.capturedImage}  className="rounded img-fluid   take-picture" alt="User Image" />
                                  } 
                                  
                                  {this.state.capturedImage===""?
                                    <div className="text-center">                                  
                                        <i class="fas fa-camera text-primary fa-3x"data-toggle="tooltip" title="Take Picture" onClick={e=>this.capture()} ></i>
                                    </div>
                                :<div className="text-center">
                                      <i class="fas fa-retweet text-primary fa-3x" data-toggle="tooltip" title="Re-Take" onClick={e=>this.setState({capturedImage:""})}></i> 
                                      <i class="fas fa-save text-primary fa-3x ml-2"data-toggle="tooltip" data-placement="right"
                                                title="Save Image"  onClick={e=>this.updateProfilePic()}></i></div>}
                               </span>
                           </div>
                      
                       
                       </div>
                   </form>
                  </Popup>
                {this.state.showPopup===true?
                 <Popup
                   open={this.state.showPopup}
                   closeOnDocumentClick={false}
                   onClose={e=>this.setState({newPrescriptionPopup:false,showPopup:!this.state.showPopup})}
                 >
                 <div className="model-close p-3">
                    <h3 className="mt0 mb0"><b>{this.state.newPrescriptionPopup===true?"Prescription":"New Scan"}</b>
                       <a className="close" onClick={e=>this.closeHandle()}>
                        &times;
                        </a></h3>
                   </div>
                  
          <hr className="mt0 mb0"/>
                    {this.state.newPrescriptionPopup===true?
                    <form className="p-3">
                       <div className="form-row">
                           <div className="form-group col-md-6">
                               <label htmlFor="inputCity">Drug Name</label>
                               <input type="text" name="drug_name" value={this.state.prescriptionObj.drug_name} 
                               onChange={e=>{this.handleCreatePrescription(e)}} 
                               onKeyUp={e=>{this.handleCreatePrescription(e)}} 
                               className="form-control"   />
                           </div>
                           </div>
                           <div className="form-row">
                           <div className="form-group col-md-6">
                               <label htmlFor="inputCity">Dosage</label>
                               <input type="text" name="dosage" value={this.state.prescriptionObj.dosage} onChange={e=>{this.handleCreatePrescription(e)}} onKeyUp={e=>{this.handleCreatePrescription(e)}} className="form-control"   />
                           </div>
                           </div>
                           <div className="form-row">
                           <div className="form-group col-md-6">
                               <label htmlFor="inputCity">Frequency</label>
                               <input type="text" name="frequency" value={this.state.prescriptionObj.frequency} onChange={e=>{this.handleCreatePrescription(e)}} onKeyUp={e=>{this.handleCreatePrescription(e)}} className="form-control"   />
                           </div>
                           </div>
                            
                            
                      
                       
                       <div className="text-right">
                       {this.state.openType=="update"?
                       <button type="button"onClick={()=>this.actionPrescription({},"update")}   className="btn btn-primary border-0"><i className="fas fa-save text-white mt-1 mr-1"></i>Update</button>
                       :
                     <button type="button"onClick={()=>this.actionPrescription({},"save")}   className="btn btn-primary border-0"><i className="fas fa-save text-white mt-1 mr-1"></i>Save</button>
                     }
                       </div>
                   </form>
                    :
                       <form className="p-3">
                            <div className="form-group row">
                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 mt-1">
                                    <div className="form-group">
                                        <label className="col-form-label" htmlFor="inputTitle">Scan Title</label>
                                        <input type="text" className="form-control" id="inputTitle"
                                        name="scanName" 
                                        onChange={e=>{this.handleChange(e)}}
                                        onKeyUp={e=>{this.handleChange(e)}}
                                        value={this.state.newScan.scanName} />
                                    </div>
                                </div>
                                {/* <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 mt-1">
                                    <div className="form-group">
                                        <label className="col-form-label" htmlFor="selectStatus">Status</label>
                                        <select name="status" value={this.state.newScan.status} onKeyUp={e=>{this.handleChange(e)}} className="form-control" id="selectStatus">
                                            <option  value="">Please select</option>
                                        {this.state.statusOption.map((status,index)=> 
                                    
                                        <option key={index} value={status} >{status}</option>
                                    )}
                                        </select>
                                    </div>
                                </div> */}
                                
                            </div>
                            <div className="text-right">
              <button type="button"onClick={e=>this.createScan()} className="btn btn-primary border-0"><i className="fas fa-save text-white mt-1 mr-1"></i>Save</button>
            </div>
            </form>}
                        
                 </Popup>:null}

                <section role="main" className="content-body">
          <header className={'page-header ' + (this.state.showMenu ? '' : 'header-left')}>
            <h2><b>Patient Details -</b>{this.state.patient.firstname} {this.state.patient.lastname}</h2>
          </header>
                    <div className="mt10">
                 </div>
					<div className="row">
						<div className="col-xl-3 col-lg-3 col-md-4 col-sm-12 mb-4 mb-xl-0 mt-sm-3">
							<section className="card">
								<div className="card-body">
									<div className="thumb-info mb-3">
										<img src={this.state.patient.profileImg?baseUrl+this.state.patient.profileImg:userImg}  className="rounded img-fluid" alt="User Image" />
										<div className="thumb-info-title">
											<span className="thumb-info-inner">{this.state.patient.firstname} {this.state.patient.lastname}</span>
										</div>
                                      
									</div>
                                    <div className="text-center"> 
                                    {/* <button className="btn btn-primary"  >  */}
                                    {this.state.editMode?
                                     <div className="text-center">
                                        
                                      <i class="fas fa-camera  text-primary fa-3x " data-toggle="tooltip" data-placement="right"title="Open Webcam"  onClick={e=>{this.openWebCam()}}></i>
                                    </div>
                                    :null}
                                     {/* Take Photo</button> */}
                                    {/* <button className="btn btn-primary" > */}
                                                {/* <div class="container">
                                                    <span class="select-wrapper">
                                                        <input type="file" class="filestyle" name="image_src" id="image_src" data-input="false" data-iconName="fa fa-upload" data-buttonText="Upload File" />
                                                    </span>
                                                </div> */}
                                         {/* <i class="fas fa-image text-primary  fa-3x ml-3"  type="file" onChange={e=>this.onChangeFile(e)} > */}
                                         {/* <input id="myInput"
                                            type="file"
                                                ref={(ref) => this.upload = ref}
                                                 onClick={e=>this.onChangeFile(e)}
                                                onChange={e=>this.onChangeFile(e)}  /> */}
                                                
                                         {/* </i>  */}
                                         {/* Choose Image</button> */}
                                   
                                 </div>
								</div>
							</section>
						</div>
                        <div className="col-xl-6 col-lg-6 col-md-8 col-sm-12 patient-result">
							 
                                 <div className="custom-tabs">
                                    <Tabs>
                                    <TabList>
                                    <Tab>Patient Details</Tab>
                                    <Tab >Prescriptions</Tab>
                                    {/* <Tab >Diagnosis</Tab> */}
                                    </TabList>
                                    <TabPanel>
                                        
                                     
                                    <form className="p-3">
                                    {!this.state.editMode?<div  className="form-row">
                                            <div className="form-group col-md-12">
                                            <i className="fas fa-edit float-right pt5" onClick={e=>this.setState({editMode:!this.state.editMode})}></i>
                                                </div>
                                     </div>:null}

                                    {this.state.editMode===true?<div  className="form-row">
                                            <div className="form-group col-md-6">
                                                <label htmlFor="inputAddress" className="form-group ">First Name </label>
                                                <input type="text"   name="firstname"
                                                 onChange={e=>this.handleatientDetailChange(e)}
                                                  onKeyUp={e=>this.handleatientDetailChange(e)}   value={this.state.patient.firstname?this.state.patient.firstname:""} className="form-control" id="inputAddress"   />
                                            </div>
                                             <div className="form-group col-md-6">
                                                <label htmlFor="inputAddress" className="form-group ">Last Name </label>
                                                <input type="text"  name="lastname"
                                                 onChange={e=>this.handleatientDetailChange(e)} 
                                                 onKeyUp={e=>this.handleatientDetailChange(e)}
                                                   value={this.state.patient.lastname?this.state.patient.lastname:""}
                                                    className="form-control" id="inputAddress"   />
                                            </div>
                                         </div>:null}

                                         <div  className="form-row">
                                            <div className="form-group col-md-6">
                                                <label htmlFor="inputAddress" className="form-group ">Gender </label>
                                                <input type="text"   name="gender"
                                                 onChange={e=>this.handleatientDetailChange(e)}
                                                  onKeyUp={e=>this.handleatientDetailChange(e)}  
                                                  disabled={!this.state.editMode}
                                                   value={this.state.patient.gender?this.state.patient.gender:""} className="form-control" id="inputAddress"   />
                                            </div>
                                             <div className="form-group col-md-6">
                                                <label htmlFor="dob" className="form-group ">DOB </label><br></br>
                                                {this.state.editMode? <DatePicker data-plugin-datepicker   
                                                        className="form-control w269"
                                                         selected={this.state.patient.dob?new Date(this.state.patient.dob):""}
                                                        onChange={(date) =>{
														console.log(new Date(date));
 														this.setState(prevState => ({
															patient: {                   
																...prevState.patient,     
																dob:new Date(date)// moment(date).format('MM-DD-YYYY')       
															}
														}))
                                                    }}
                                                    onKeyUp={(date) =>{
														console.log(new Date(date));
 														this.setState(prevState => ({
															patient: {                   
																...prevState.patient,     
																dob:new Date(date)// moment(date).format('MM-DD-YYYY')       
															}
														}))
                                                    }}
													name="dob" 
                                                    dateFormat="MM-dd-yyyy" />:
                                                     <input type="text"  name="dob" disabled={true}
                                                     onChange={e=>this.handleatientDetailChange(e)} 
                                                     onKeyUp={e=>this.handleatientDetailChange(e)}
                                                       value={this.state.patient.dob?moment(this.state.patient.dob).format('MM-DD-YYYY'):""}
                                                        className="form-control" id="dob"/>}
                                                </div>
                                         </div>
                                             <div className="form-group">
                                                <label htmlFor="inputAddress" className="w-100">Address </label>
                                                <input type="text" disabled={!this.state.editMode}
                                                 name="address" onChange={e=>this.handleatientDetailChange(e)} 
                                                 onKeyUp={e=>this.handleatientDetailChange(e)}   value={this.state.patient.address?this.state.patient.address:""} className="form-control" id="inputAddress"   />
                                            </div>
                                           

                                            <div className="form-row">
                                                <div className="form-group col-md-4">
                                                    <label htmlFor="inputCity">City</label>
                                                    <input type="text" disabled={!this.state.editMode}
                                                     name="city" onChange={e=>this.handleatientDetailChange(e)} 
                                                     onKeyUp={e=>this.handleatientDetailChange(e)} value={this.state.patient.city?this.state.patient.city:""}  className="form-control" id="inputCity" />
                                                </div>
                                                <div className="form-group col-md-4">
                                                    <label htmlFor="inputState">State</label>
                                                  <input type="text" disabled={!this.state.editMode} name="state" 
                                                  value={this.state.patient.state?this.state.patient.state:""}
                                                   onChange={e=>this.handleatientDetailChange(e)}
                                                    onKeyUp={e=>this.handleatientDetailChange(e)} className="form-control"   />
                                                </div>
                                            
                                                <div className="form-group col-md-4">
                                                    <label htmlFor="inputZip">Zip Code</label>
                                                    <input type="number"  disabled={!this.state.editMode} 
                                                    name="zipcode" onChange={e=>this.handleatientDetailChange(e)} 
                                                    onKeyUp={e=>this.handleatientDetailChange(e)}
                                                      value={this.state.patient.zipcode?this.state.patient.zipcode:""}
                                                        className="form-control" id="inputZip" />
                                                </div>
                                            </div>
                                            <div className="form-row">
                                                <div className="form-group col-md-6">
                                                    <label htmlFor="inputEmail">Email</label>
                                                    <input type="text"  disabled={true} name="email" 
                                                    onChange={e=>this.handleatientDetailChange(e)}
                                                     onKeyUp={e=>this.handleatientDetailChange(e)}   value={this.state.patient.email ?this.state.patient.email:"" }  className="form-control" id="inputEmail" />
                                                </div>
                                                <div className="form-group col-md-6">
                                                    <label htmlFor="inputphone">Phone</label>
                                                    <input type="number"  disabled={!this.state.editMode}
                                                     name="phoneNumber"
                                                     onChange={e=>this.handleatientDetailChange(e)}
                                                     onKeyUp={e=>this.handleatientDetailChange(e)}  
                                                      value={this.state.patient.phoneNumber?parseInt(this.state.patient.phoneNumber):""}  className="form-control" id="inputPhone" />
                                                </div>
                                            </div>
                                            {this.state.editMode===true?
                                            <div className="text-right">
                                            <button type="button" onClick={e=>this.updateProfile()} className="btn btn-primary  mr-2 border-0"><i className="fas fa-save text-white mt-1 mr-1"></i>Update</button>
                                            <button type="button" onClick={e=>this.setState({editMode:!this.state.editMode})} className="btn btn-danger border-0  mr-2">Cancel</button>
                                            </div>
                                            :null}
                                        </form>
                
                                                </TabPanel>
                                                <TabPanel>
                                                    <div className="table-responsive">
                                                <table className="table table-bordered table-hover table-striped mt-3">
                                            <thead>
                                                <tr>
                                                    <th>Drug Name</th>
                                                    <th>Date Prescribed</th>
                                                    <th>Dosage</th>
                                                    <th>Frequency</th>
                                                    <th>Action</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                
                                                  {this.state.prescription.result?this.state.prescription.result.map((object,index)=> 
                                                    <tr key={index}>
                                                    <td>{object.drug_name}</td>
                                                    <td>{moment.utc(object.created_at).utcOffset(moment().format('Z')).format('LL')}</td>
                                                    <td>{object.dosage}</td>
                                                    <td>{object.frequency}</td>
                                                    <td>
                                                    <i className="fas fa-edit float-center pt5"onClick={()=>{ this.setState({showPopup:true,newPrescriptionPopup:true,prescriptionObj:object,openType:"update"})}}  ></i>
                                                    <div className="badge badge-error font-weight-large pull-center patient-icon"><i className="fa fa-trash text-danger mt-m5" onClick={()=>this.actionPrescription(object,"delete")}  aria-hidden="true"></i></div>
                                                    </td>
                                                    </tr>
                                                    ):null}
                                                    
                                             </tbody>
                                            
                                        </table>
                                        <button type="button" onClick={e=>{this.setState({newPrescriptionPopup:true,openType:"save",showPopup:true,prescriptionObj:{},newScan:{}})}} 
                                            className="btn btn-primary border-0 float-right"><i className="fas fa-plus"></i> Prescription</button>
                                           
                                        </div>
                                        <hr />
                                        <label htmlFor="inputAddress" className="form-group p-2">Allergy </label>
                                       {this.state.showpatientAllergy===true? <div className="form-group">
                                        <div className="form-row row mx-0">
                                        {/* <div className="col-md-3"> */}
                                            {this.state.patientAllergyList.result.map((obj,index)=>{
                                                return <div className="col-md-4">
                                                

                                                <div className="input-group mb-3">
                                                <span className="form-control text-truncate">{obj.allergy_name}</span>
                                                
                                                <div className="input-group-append">
                                                    <button type="button" className="btn btn-secondary"
                                                     onClick={e=>this.allergyAction(obj)}>X</button>
                                                </div>
                                                </div>
                                               
                                                
                                            </div>
                                            })}
                                        {/* </div> */}
                                          
                                            </div>
                                           {/* <div className="form-group text-right form-row"> 
                                           <button className="btn btn-default form-control "type="button" 
                                           onClick={e=>{this.showAllallergy()}}><i className="fas fa-plus"></i>  Add more allergy</button>                                           
    
                                           </div> */}
                                        </div>:null}
                                        
                                        <div className="form-group text-right">
                                            {this.state.showAllallergy? 
                                    <div className="form-row mb-2 mt-2">
                                        <div className="input-group input-group-sm ">
                                            <input type="text" className="form-control" name="newAllergy" value={this.state.newAllergy} onChange={e=>this.handleChangeAction(e)} onKeyUp={e=>this.handleChangeAction(e)}/>
                                            <span className="input-group-append">
                                                <button type="submit"className="btn btn-primary border-0 py-0" onClick={e=>this.createAllergy()}>Save</button>
                                            </span>
                                        </div>
                                    </div>:null

                                            }
                                        <span   onClick={e=>{}}
                                           className="btn btn-primary border-0 mr-2"
                                            onClick={e=>{this.setState({showAllallergy:true})}}><i className="fas fa-plus"></i> Add Allergy</span>

                                           </div>
                            </TabPanel>  
                            {/* <TabPanel>
                                dummy Diagnosis
                            </TabPanel> */}
                       </Tabs>
                  </div>
            </div>
                        
						<div className="col-xl-3 col-lg-3 col-md-12 col-sm-12 scan-result">
							<section className="card mt-3">
                            	<header className="card-header">
                                    <h2 className="card-title mb-2">Recent Scans</h2>
                                    <form action="#" className="search">
                                        <div className="input-group input-group-sm">
                                            <input type="text" name ="searchScan"value={this.state.searchScan} 
                                            onChange={e=>this.searchScan(e)}
                                            onKeyUp={e=>this.searchScan(e)} className="form-control form-control-md" placeholder="Search..." />
                                            <span className="input-group-append">
                                                <button className="btn btn-default py-0" type="submit"><i className="fas fa-search"></i></button>
                                                <a className="btn btn-default p-2 btn-sm" onClick={e=>{this.setState({showPopup:true,newScan:{}})}}><i className="fas fa-plus"></i> New Scan</a>
                                            </span>
                                        </div>
                                    </form>
                                </header>
                <div className="card-body">
                                    <ul className="simple-post-list">
                                    
                                 { recentScans.map((scan,index)=>
                                 <li key={index} >
                                 <div  className="post-info ">
                                     <a className="pointer mb-2 pb-2" onClick={()=>{this.HistoryPush('scan');this.props.selectRecentScan(scan)}}>{scan.scanName}</a>
                                    
                                     <div onClick={()=>this.deleteScan(scan)} className="badge badge-error font-weight-large pull-right patient-icon"><i className="fa fa-trash text-danger mt-m5" aria-hidden="true"></i></div>
                                     <span className="badge badge-success font-weight-normal pull-right">{scan.status}</span> <br />
                                    <span className="badge badge-pill badge-primary font-weight-normal mt-2">{scan.discovery.length}</span> Discoveries
                                     <div className="post-meta">{moment.utc(scan.created_at).utcOffset(moment().format('Z')).format('LL')}  </div>
                 
                                 </div>
                             </li>
                                    )}
                                        
                                    </ul>
                </div>
                            </section>
                          </div>
                         </div>
                        </section>
                        <ToastContainer/>
                    </div>
        )
    }
}
 const mapStateToProps = (state) => {
     return {
        patientId:state.patientReducer.patientId,
        recentScans:state.scanReducer.recentScans ,
        patinetPriscription:state.patientReducer.patinetPriscription ,
        patientDetail:state.patientReducer.patientObj
        
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
         getRecentScans:(obj,patientId)=>{dispatch(scanAction.getRecentScans(obj,patientId))},
        selectRecentScan:(scanObj)=>{dispatch({type:types.SELECTED_SCAN,payload:scanObj})},
        getPrescription:(obj,patientId)=>{dispatch(userAction.getPrescription(obj,patientId))},
        getPatientDetails:(patientId)=>{dispatch(userAction.getPatientDetails(patientId))},

     }
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ScanConponent));
// export default connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(DATA);