import * as types from "./types";
 import {baseUrlMl, baseUrl,headers } from "../config";
import Axios from 'axios'
 
export const discovery = (obj) => {
    return Axios.post(baseUrlMl + `/discovery`,obj,headers).then(res=>{
        return res['data']
           }).catch(err => {
             return {payload:err} 
      })
}
 
export const Inference = (obj) => {
    return Axios.post(baseUrlMl + `/inference`,obj,headers).then(res=>{
          return res['data']
           }).catch(err => {
             return {payload:err} 
      })
   
}

