import * as types from "./types";
 import { baseUrl,headers,baseUrlMl } from "../config";
import Axios from 'axios'

 
export const saveTrainedImage = (obj) => {

       return Axios.post(baseUrlMl + `/save`,obj,headers).then(res=>{

                  return {payload:res} 
              }) 
      
}
export const getannotatedImages = (obj) => {

  return Axios.post(baseUrlMl + `/getAnnotatedImages`,obj,headers).then(res=>{
            return res['data']
         }) 
 
}
  
  export const testApi = (obj) => {
    return Axios.post(baseUrlMl + `/test`,obj,headers).then(res=>{
          return res['data']
           }) 
   
}