 
import  * as types from "../actions/types";

const initialState={
 recentScans:[],
 selected_scan:{},
 scanImages:[],
errors:null
};
    

const scanReducer = (state = initialState, action) => {
 
switch(action.type){
    case types.GET_RECENT_SCANS:{
        return {...state,recentScans:action.payload}
    }
    case types.SELECTED_SCAN:{
        return {...state,selected_scan:action.payload}
    }
    case types.GET_SCAN_IMAGES:{
        return {...state, scanImages:action.payload}
    }
    
    default:{
        return {...state,errors:null};
    }
}
}

export default scanReducer;